import React from 'react'
import NavHome from "./component/NavHome"
import Footer from "./component/Footer"
import { useEffect, useState } from 'react'
import axios from "axios"

function Home() {

    const [Data, setData] = useState([]);
    const [Data2, setData2] = useState([]);
    const [Data3, setData3] = useState([]);
    const [Data4, setData4] = useState([]);

    useEffect(() => {
        getStudent();
        getNumStudent();
        getCardTeacher();
        getCardTeacherFirst();
    }, []);

    const getStudent = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `student.php/?xCase=0`);
            setData(response.data);
            console.log(response.data)
        } catch (error) {
            console.error(error);
        }
    };

    const getNumStudent = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `student.php/?xCase=6`);
            setData2(response.data);
            console.log(response.data)
        } catch (error) {
            console.error(error);
        }
    };

    const getCardTeacher = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `teacher.php/?xCase=6`);
            setData3(response.data);
            console.log(response.data)
        } catch (error) {
            console.error(error);
        }
    };

    const getCardTeacherFirst = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `teacher.php/?xCase=5`);
            setData4(response.data);
            console.log(response.data)
        } catch (error) {
            console.error(error);
        }
    };

    return (
        <div className="">
            <NavHome />
            {/* Banner */}
            <div id="carouselExampleCaptions" className="carousel slide" data-bs-ride="carousel">
                <div className="carousel-indicators">
                    <button type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide-to="0" className="active" aria-current="true" aria-label="Slide 1"></button>
                    <button type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide-to="1" aria-label="Slide 2"></button>
                    <button type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide-to="2" aria-label="Slide 3"></button>
                    <button type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide-to="3" aria-label="Slide 4"></button>
                </div>
                <div className="carousel-inner">
                    <div className="carousel-item active">
                        <img src={process.env.REACT_APP_PATH + `Image/imgBanner/1.png`} className="d-block w-100" alt="..." />
                    </div>
                    <div className="carousel-item">
                        <img src={process.env.REACT_APP_PATH + `Image/imgBanner/2.png`} className="d-block w-100" alt="..." />
                    </div>
                    <div className="carousel-item">
                        <img src={process.env.REACT_APP_PATH + `Image/imgBanner/3.png`} className="d-block w-100" alt="..." />
                    </div>
                    <div className="carousel-item">
                        <img src={process.env.REACT_APP_PATH + `Image/imgBanner/4.png`} className="d-block w-100" alt="..." />
                    </div>
                </div>
                <button className="carousel-control-prev" type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide="prev">
                    <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span className="visually-hidden">Previous</span>
                </button>
                <button className="carousel-control-next" type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide="next">
                    <span className="carousel-control-next-icon" aria-hidden="true"></span>
                    <span className="visually-hidden">Next</span>
                </button>
            </div >

            <div className="container my-5">
                <section>
                    <div className="row">
                        <div className="col-lg-5">
                            <img src={process.env.REACT_APP_PATH + `Image/H-1.jpg`} alt="รูปประวัติโรงเรียน" className="container-fluid" />
                        </div>
                        <div className="col-lg-7">
                            <div className="bold-pur mt-2 fs-2 ps-3">ประวัติโรงเรียนบ้านน้ำจำ</div>
                            <div className="subtitle fs-4 ps-3">สำนักงานเขตพื้นที่การศึกษาประถมศึกษาเชียงรายเขต 3</div>
                            <br /><div className="descript fs-5 ps-3">เปิดครั้งแรกเมื่อวันที่ 1 พฤษภาคม 2501 โดยนายเสาร์ แก้วคำมา ผู้ใหญ่บ้านและชาวบ้านได้ร่วมใจกันสร้างอาคารเรียนชั่วคราวขึ้น โดยมี นายจันทร์ พิมพ์เงิน เป็นครูใหญ่ มีเนื้อที่ทั้งสิ้น 9 ไร่1 งาน 40 ตารางวา 300 โดย โรงเรียนบ้านน้ำจำจะตั้งอยู่ที่ หมู่ 12 ตำบลโป่งผา อำเภอแม่สาย จังหวัดเชียงราย 57130</div>
                        </div>
                    </div>
                </section>
            </div>

            <section className='img-backg py-5 my-5'>
                <div className="container">
                    <div className="row">
                        <div className="col-lg-5 text-center my-auto">
                            <div className="t-studentnum">{Data.length}</div>
                            <div className="bold-pur fs-2">จำนวนนักเรียนทั้งหมด</div>
                        </div>
                        <div className="col-lg-7 text-center">
                            <div className='row'>
                                {Data2.map((Datas, key) => (
                                    <div className='col-6 col-lg-4 my-2'>
                                        <div class="circle d-flex justify-content-center">
                                            <div class="circle__inner">
                                                <div class="circle__wrapper">
                                                    <div class="circle__content color theme bold-pur">{Datas.NumStudent}<br />
                                                        <div className='fs-5 text-classroom'>{Datas.class_name}</div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>
                    </div>
                </div>
            </section >

            <div className="container my-5">
                <section>
                    <div className='bold-pur text-center fs-2'>บุคคลากร</div>
                    <div className="row">
                        <div className="col-lg-3 col-md-6 mx-auto my-3 pointer" data-bs-toggle="modal" data-bs-target="#staticBackdrop">
                            <div className='card card-teacher border-light p-2 mt-3 w-100 h-100'>
                                <img src={process.env.REACT_APP_API + `teachers/${Data4.teacher_img}`} className="card-img-top object-fit-cover" style={{ height: '350px' }} alt="ผอ." />
                                <div className="card-body text-center">
                                    <div className="card-text bold-pur fs-5">{Data4.teacher_prefix}{Data4.teacher_firstname} {Data4.teacher_lastname}</div>
                                    <div className='subtitle fs-5'>{Data4.teacher_department}</div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        {Data3.map((Datas, key) => (
                            <div className="col-lg-3 col-md-6 mx-auto my-3 pointer">
                                <div className='w-100 h-100' data-bs-toggle="modal" data-bs-target={`#staticBackdropT` + key}>
                                    <div className='card card-teacher border-light p-2 mt-3 w-100 h-100'>
                                        <img src={process.env.REACT_APP_API + `teachers/${Datas.teacher_img}`} className="card-img-top object-fit-cover" style={{ height: '300px' }} alt="" />
                                        <div className="card-body text-center">
                                            <div className="card-text bold-pur fs-5">{Datas.teacher_prefix}{Datas.teacher_firstname} {Datas.teacher_lastname}</div>
                                            <div className='subtitle fs-5'>{Datas.teacher_department}</div>
                                        </div>
                                    </div>
                                </div>
                                {/* Modal */}
                                <div class="modal fade" id={`staticBackdropT` + key} data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
                                    <div class="modal-dialog modal-dialog-centered">
                                        <div class="modal-content">
                                            <div class="modal-body">
                                                <div className="text-end">
                                                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                                                </div>
                                                <div className='text-center'>
                                                    <img src={process.env.REACT_APP_API + `teachers/${Datas.teacher_img}`} className='w-50 rounded' alt="" />
                                                    <div className="fs-6 fw-bold my-2 color theme">{Datas.teacher_prefix}{Datas.teacher_firstname} {Datas.teacher_lastname}</div>
                                                    <div className="row">
                                                        <div className="col-5 text-end">
                                                            <div className='fs-6 text-classroom'>ตำแหน่ง : </div>
                                                        </div>
                                                        <div className="col-7 text-start">
                                                            <div className='fs-6 text-classroom'>{Datas.teacher_department}</div>
                                                        </div>
                                                        <div className="col-5 text-end">
                                                            <div className='fs-6 text-classroom'>ประเภท : </div>
                                                        </div>
                                                        <div className="col-7 text-start">
                                                            <div className='fs-6 text-classroom'>{Datas.typeTeacher_name}</div>
                                                        </div>
                                                        <div className="col-5 text-end">
                                                            <div className='fs-6 text-classroom'>วิทยฐานะ : </div>
                                                        </div>
                                                        <div className="col-7 text-start">
                                                            <div className='fs-6 text-classroom'>{Datas.academic_name}</div>
                                                        </div>
                                                        <div className="col-5 text-end">
                                                            <div className='fs-6 text-classroom'>เบอร์ที่ติดต่อ : </div>
                                                        </div>
                                                        <div className="col-7 text-start">
                                                            <div className='fs-6 text-classroom'>{Datas.teacher_phone}</div>
                                                        </div>
                                                        <div className="col-5 text-end">
                                                            <div className='fs-6 text-classroom'>ไลน์ไอดี : </div>
                                                        </div>
                                                        <div className="col-7 text-start">
                                                            <div className='fs-6 text-classroom'>{Datas.teacher_line}</div>
                                                        </div>
                                                        <div className="col-5 text-end">
                                                            <div className='fs-6 text-classroom'>อีเมล : </div>
                                                        </div>
                                                        <div className="col-7 text-start">
                                                            <div className='fs-6 text-classroom'>{Datas.teacher_email}</div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                        ))}
                    </div>


                    {/* Modal */}
                    <div className="modal fade" id="staticBackdrop" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
                        <div className="modal-dialog modal-dialog-centered">
                            <div className="modal-content">
                                <div className="modal-body">
                                    <div className="text-end">
                                        <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                                    </div>
                                    <div className='text-center'>
                                        <img src={process.env.REACT_APP_API + `teachers/${Data4.teacher_img}`} className='w-50 rounded' alt="" />
                                        <div className="fs-6 fw-bold my-2 color theme">{Data4.teacher_prefix}{Data4.teacher_firstname} {Data4.teacher_lastname}</div>
                                        <div className="row">
                                            <div className="col-5 text-end">
                                                <div className='fs-6 text-classroom'>ตำแหน่ง : </div>
                                            </div>
                                            <div className="col-7 text-start">
                                                <div className='fs-6 text-classroom'>{Data4.teacher_department}</div>
                                            </div>
                                            <div className="col-5 text-end">
                                                <div className='fs-6 text-classroom'>ประเภท : </div>
                                            </div>
                                            <div className="col-7 text-start">
                                                <div className='fs-6 text-classroom'>{Data4.typeTeacher_name}</div>
                                            </div>
                                            <div className="col-5 text-end">
                                                <div className='fs-6 text-classroom'>วิทยฐานะ : </div>
                                            </div>
                                            <div className="col-7 text-start">
                                                <div className='fs-6 text-classroom'>{Data4.academic_name}</div>
                                            </div>
                                            <div className="col-5 text-end">
                                                <div className='fs-6 text-classroom'>เบอร์ที่ติดต่อ : </div>
                                            </div>
                                            <div className="col-7 text-start">
                                                <div className='fs-6 text-classroom'>{Data4.teacher_phone}</div>
                                            </div>
                                            <div className="col-5 text-end">
                                                <div className='fs-6 text-classroom'>ไลน์ไอดี : </div>
                                            </div>
                                            <div className="col-7 text-start">
                                                <div className='fs-6 text-classroom'>{Data4.teacher_line}</div>
                                            </div>
                                            <div className="col-5 text-end">
                                                <div className='fs-6 text-classroom'>อีเมล : </div>
                                            </div>
                                            <div className="col-7 text-start">
                                                <div className='fs-6 text-classroom'>{Data4.teacher_email}</div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                </section>
            </div >
            <Footer />
        </div >
    )
}
export default Home