import React from 'react'
import { Link, useNavigate } from 'react-router-dom'
import Navparent from '../component/Navparent'
import Footer from '../component/Footer'
import { useEffect, useState } from 'react'
import { checkTokenAndRedirect } from '../component/authParent'
import axios from 'axios'
import Swal from 'sweetalert2'

import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';

// import required modules
import { Autoplay, Pagination, Navigation } from 'swiper/modules';


function Parent() {

    const navigate = useNavigate();
    const localstorage = localStorage.getItem('TokenParent');
    const useToken = JSON.parse(localstorage);
    const [Data, setData] = useState([]);
    const [Data2, setData2] = useState([]);
    const [Data3, setData3] = useState([]);
    const [Data4, setData4] = useState([]);
    const [Data5, setData5] = useState([]);

    useEffect(() => {
        checkTokenAndRedirect(navigate);
        // getData();
        getData2();
    }, []);

    const updateData = (student_id) => {
        axios
            .post(process.env.REACT_APP_API + `timeinout.php/${student_id}/?xCase=6`)
            .then(function (response) {
                console.log(response.data);
                getData2();
            })
            .catch(function (error) {
                console.error(error);
            });
    };

    function showAlertUpdate(student_id) {
        Swal.fire({
            title: 'บันทึกข้อมูล?',
            text: "ยืนยันที่จะบันทึกข้อมูลสถานะการกลับบ้านของนักเรียน",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#009B19',
            cancelButtonColor: '#6c757d',
            confirmButtonText: 'ยืนยัน',
            cancelButtonText: 'ยกเลิก'
        }).then((result) => {
            if (result.isConfirmed) {
                console.log(student_id)
                updateData(student_id)
                Swal.fire({
                    position: 'top-center',
                    icon: 'success',
                    title: 'บันทึกข้อมูลสำเร็จ',
                    text: "คุณได้ทำการยืนยันข้อมูลการกลับบ้านเรียบร้อยแล้ว!",
                    showConfirmButton: false,
                    timer: 1500
                }
                )
            }
        })
    }

    function getCurrentDate() {
        const monthsInThai = [
            "มกราคม",
            "กุมภาพันธ์",
            "มีนาคม",
            "เมษายน",
            "พฤษภาคม",
            "มิถุนายน",
            "กรกฎาคม",
            "สิงหาคม",
            "กันยายน",
            "ตุลาคม",
            "พฤศจิกายน",
            "ธันวาคม"
        ];

        const today = new Date();
        const day = today.getDate();
        const month = monthsInThai[today.getMonth()]; // เลือกชื่อเดือนในภาษาไทย
        const year = today.getFullYear() + 543;

        return `${day} ${month} ${year}`;
    }

    // const getData = async () => {
    //     try {
    //         const response = await axios.get(process.env.REACT_APP_API + `student.php/${useToken.parent_id}/?xCase=9`);
    //         setData(response.data);
    //         console.log("data1", response.data)
    //     } catch (error) {
    //         console.error(error);
    //     }
    // };

    const getData2 = async () => {
        try {
            const response1 = await axios.get(process.env.REACT_APP_API + `student.php/${useToken.parent_id}/?xCase=9`);
            const data1 = response1.data;
            setData(data1); // กำหนดค่าให้ Data
            console.log("data1", data1);

            const data5Array = []; // สร้างอาร์เรย์เปล่าเพื่อเก็บข้อมูลพฤติกรรมล่าสุด
            for (const student of data1) {
                const response5 = await axios.get(process.env.REACT_APP_API + `student.php/${useToken.parent_id}/${student.student_id}/?xCase=13`);
                const responseData5 = response5.data;
                data5Array.push(responseData5);
            }
            setData5(data5Array);
            console.log("data5", data5Array);

            const data4Array = []; // สร้างอาร์เรย์เปล่าเพื่อเก็บข้อมูลการบ้านล่าสุด
            for (const student of data1) {
                const response4 = await axios.get(process.env.REACT_APP_API + `student.php/${useToken.parent_id}/${student.student_id}/?xCase=12`);
                const responseData4 = response4.data;
                data4Array.push(responseData4);
            }
            setData4(data4Array);
            console.log("data4", data4Array);

            const data3Array = []; // สร้างอาร์เรย์เปล่าเพื่อเก็บข้อมูลครูประจำชั้น
            for (const student of data1) {
                const response3 = await axios.get(process.env.REACT_APP_API + `student.php/${useToken.parent_id}/${student.student_id}/?xCase=11`);
                const responseData3 = response3.data;
                data3Array.push(responseData3);
            }
            setData3(data3Array);
            console.log("data3", data3Array);

            const data2Array = []; // สร้างอาร์เรย์เปล่าเพื่อเก็บข้อมูลเวลาเข้าออก
            for (const student of data1) {
                const response2 = await axios.get(process.env.REACT_APP_API + `student.php/${useToken.parent_id}/${student.student_id}/?xCase=10`);
                const responseData2 = response2.data;
                data2Array.push(responseData2); // เพิ่มข้อมูลของแต่ละนักเรียนลงในอาร์เรย์
            }
            setData2(data2Array); // กำหนดค่าให้ Data2 เป็นอาร์เรย์ที่เก็บข้อมูลของทุกนักเรียน
            console.log("data2", data2Array);
        } catch (error) {
            console.error(error);
        }
    };



    return (
        <div>
            <Navparent />
            <section>
                {Data.map((Datas) => (
                    <div className="card mb-4 ">
                        <div className="container card-body">
                            <div className='row'>
                                <div className='col-lg-5 my-3'>
                                    <div className="card shadowcard p-2 px-lg-5 h-100">
                                        <div className="card-body d-flex justify-content-center ">
                                            <img src={process.env.REACT_APP_API + `students/` + Datas.student_img} className='rounded-circle object-fit-cover' alt="รูปเด็กนักเรียน" width={"150px"} height={"150px"} />
                                        </div>
                                        <div className='mx-auto fs-4 fw-bold'>ชั้น{Datas.class_name} {Datas.classroom_name}</div>
                                        <hr className='w-100 mx-auto' />
                                        <div className='fs-5'><strong>รหัสนักเรียน : </strong>{Datas.student_id}</div>
                                        <div className='fs-5'><strong>ชื่อ-นามสกุล : </strong>{Datas.student_prefix}{Datas.student_firstname} {Datas.student_lastname}</div>
                                        <div className='fs-5'><strong>ชื่อเล่น : </strong>{Datas.student_nickname}</div>
                                        <div className='fs-5'><strong>อายุ : </strong>{Datas.student_age} ปี</div>
                                        <div className="col text-center my-2">
                                            <Link to={`/ParentStudent/${Datas.student_id}`} className='btn btn-outline-themepink themepink w-50 fs-6 mx-auto'>ข้อมูลนักเรียน</Link>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-lg-7">
                                    <div className="col-lg-12 my-3">
                                        <div className="card shadowcard">
                                            <div className="card-body">
                                                <div className="color bgpink text-white rounded w-100 fs-5 fw-bold text-center p-2">วันที่ {getCurrentDate()}</div>
                                                <div className='mt-4'>
                                                    <div >
                                                        <div className='row mx-auto my-2'>
                                                            <div className="col-lg-7 my-auto fs-5">เวลาที่เข้าโรงเรียน</div>
                                                            <div className="col-lg-5 fs-5">
                                                                <div href="" className='color bginout text-center rounded p-1'>
                                                                    {Data2.filter((studentDataArray) => studentDataArray.some((Datas2) => Datas2.student_id === Datas.student_id)).map((studentDataArray) => (
                                                                        studentDataArray.map((Datas2) => (
                                                                            <span key={Datas2.student_id}>
                                                                                {Datas2.timeIn ? `${Datas2.timeIn} น.` : 'ยังไม่มีข้อมูล'}
                                                                            </span>
                                                                        ))
                                                                    ))}
                                                                    {Data2.filter((studentDataArray) => studentDataArray.some((Datas2) => Datas2.student_id === Datas.student_id)).length === 0 && (
                                                                        <span>ยังไม่มีข้อมูล</span>
                                                                    )}
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div className='row mx-auto my-2'>
                                                            <div className="col-lg-7 my-auto fs-5">เวลาที่ออกจากโรงเรียน</div>
                                                            <div className="col-lg-5 fs-5">
                                                                <p href="" className='color bginout text-center rounded p-1'>
                                                                    {Data2.filter((studentDataArray) => studentDataArray.some((Datas2) => Datas2.student_id === Datas.student_id)).map((studentDataArray) => (
                                                                        studentDataArray.map((Datas2) => (
                                                                            <span key={Datas2.student_id}>
                                                                                {Datas2.timeOut ? `${Datas2.timeOut} น.` : 'ยังไม่มีข้อมูล'}
                                                                            </span>
                                                                        ))
                                                                    ))}
                                                                    {Data2.filter((studentDataArray) => studentDataArray.some((Datas2) => Datas2.student_id === Datas.student_id)).length === 0 && (
                                                                        <span>ยังไม่มีข้อมูล</span>
                                                                    )}
                                                                </p>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-lg-12 my-3">
                                        <div className="card shadowcard">
                                            <div className="card-body">
                                                <div className="color bgstate text-white rounded w-100 fs-5 fw-bold text-center p-2">สถานะการกลับถึงบ้านของนักเรียน</div>
                                                <div className='m-3'>
                                                    <div className='text-center my-3 fs-5'>
                                                        ผู้ปกครองกดปุ่มเพื่อยืนยันการกลับบ้านของนักเรียน
                                                    </div>

                                                    <div className="text-center w-50 mx-auto fs-5">
                                                        {Data2.filter((studentDataArray) => studentDataArray.some((Datas2) => Datas2.student_id === Datas.student_id)).map((studentDataArray) => (
                                                            studentDataArray.map((Datas2) => (
                                                                <span key={Datas2.student_id}>
                                                                    {Datas2.statusGoHome == 0 ?
                                                                        <button className='btn btn-warning border text-white rounded py-2 w-100' onClick={() => showAlertUpdate(Datas.student_id)}>กำลังกลับบ้าน</button>
                                                                        : <button className='btn btn-success border text-white rounded py-2 w-100'>กลับถึงบ้านแล้ว</button>
                                                                    }
                                                                </span>
                                                            ))
                                                        ))}
                                                        {Data2.filter((studentDataArray) => studentDataArray.some((Datas2) => Datas2.student_id === Datas.student_id)).length === 0 && (
                                                            <div><button className='btn btn-secondary'>ยังไม่เข้าโรงเรียน</button></div>
                                                        )}
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className='col-lg-5 my-3'>
                                    <Swiper
                                        spaceBetween={30}
                                        // centeredSlides={true}
                                        autoplay={{
                                            delay: 3500,
                                            disableOnInteraction: false,
                                        }}
                                        pagination={{
                                            clickable: true,
                                        }}
                                        // navigation={true}
                                        modules={[Autoplay, Pagination]}
                                        className="mySwiper shadowcard"
                                    >
                                        {Data3.filter((studentDataArray) => studentDataArray.some((Datas2) => Datas2.student_id === Datas.student_id)).map((studentDataArray) => (
                                            studentDataArray.map((Datas3) => (
                                                <SwiperSlide>
                                                    <span key={Datas3.student_id}>
                                                        <div className="card p-2 px-lg-5 mb-3">
                                                            <div className="card-title">
                                                                <div className='fs-5 fw-bold text-center my-2'>ครูประจำชั้น</div>
                                                                <div className='fs-5 text-center my-2'>{Datas3.teacher_prefix}{Datas3.teacher_firstname} {Datas3.teacher_lastname}</div>
                                                            </div>
                                                            <div className='card-body'>
                                                                <div className=" d-flex justify-content-center ">
                                                                    <img src={process.env.REACT_APP_API + `teachers/` + Datas3.teacher_img} className='rounded-circle object-fit-cover my-auto' alt="รูปเด็กนักเรียน" width={"150px"} height={"150px"} />
                                                                </div>
                                                                <div className="fs-5 text-center my-3">
                                                                    <div className='my-1'><i class="fa-solid fa-phone me-3"></i>{Datas3.teacher_phone}</div>
                                                                    <div className='my-1'><i class="fa-brands fa-line me-3"></i>{Datas3.teacher_line}</div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </span>
                                                </SwiperSlide>

                                            ))
                                        ))}
                                    </Swiper>
                                </div>
                                <div className='col-lg-7'>
                                    <div className="col-lg-12 my-3">
                                        <div className="card shadowcard">
                                            <div className="card-body">
                                                <div className="fs-5 fw-bold p-2">การบ้านล่าสุด</div>
                                                <div className="pink-underline"></div>
                                                <div className='my-1 mx-3'>
                                                    <div className='fs-6 my-3'>
                                                        {Data4.filter((studentDataArray) => studentDataArray.some((Datas4) => Datas4.student_id === Datas.student_id)).map((studentDataArray) => (
                                                            studentDataArray.map((Datas4) => (
                                                                <div key={Datas4.student_id}>
                                                                    <p className='text-truncate'>{Datas4.subject_name} : {Datas4.homework_details}</p>
                                                                    <p className='text-truncate'>กำหนดส่ง : วันที่ {Datas4.thaidua_date}</p>
                                                                </div>
                                                            ))
                                                        ))}
                                                        {Data4.filter((studentDataArray) => studentDataArray.some((Datas4) => Datas4.student_id === Datas.student_id)).length === 0 && (
                                                            <div className='fs-6'>ยังไม่มีข้อมูลการบ้าน</div>
                                                        )}
                                                    </div>
                                                    <div className="text-center w-50 mx-auto">
                                                        <Link to={`/ParentHomework/${Datas.student_id}`} className='btn btn-outline-themepink themepink fs-6 mx-auto'>ดูการบ้านเพิ่มเติม</Link>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-lg-12 my-3">
                                        <div className="card shadowcard">
                                            <div className="card-body">
                                                <div className="fs-5 fw-bold p-2">พฤติกรรมครั้งล่าสุด</div>
                                                <div className="pink-underline"></div>
                                                <div className='my-1 mx-3'>
                                                    <div className="fs-6 my-3"></div>
                                                    {Data5.filter((studentDataArray) => studentDataArray.some((Datas5) => Datas5.student_id === Datas.student_id)).map((studentDataArray) => (
                                                        studentDataArray.map((Datas5) => (
                                                            <div key={Datas5.student_id}>
                                                                <p className='textcut'>{Datas5.behaviour_details}</p>
                                                            </div>
                                                        ))
                                                    ))}
                                                    {Data5.filter((studentDataArray) => studentDataArray.some((Datas5) => Datas5.student_id === Datas.student_id)).length === 0 && (
                                                        <p>ยังไม่มีข้อมูลการรายงานพฤติกรรมของนักเรียน</p>
                                                    )}
                                                    <div className="text-center w-50 mx-auto">
                                                        <Link to={`/ParentBehavior/${Datas.student_id}`} className='btn btn-outline-themepink themepink fs-6 mx-auto'>ดูพฤติกรรมเพิ่มเติม</Link>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                ))}
            </section>
            <Footer />
        </div>
    )
}

export default Parent
