import React from 'react'
import { Link, useNavigate, useParams } from 'react-router-dom'
import Navparent from '../component/Navparent'
import Footer from '../component/Footer'
import { useEffect, useState } from 'react'
import { checkTokenAndRedirect } from '../component/authParent'
import axios from 'axios'
import Swal from 'sweetalert2'

function Parentprofile() {

    const navigate = useNavigate();
    const localstorage = localStorage.getItem('TokenParent');
    const useToken = JSON.parse(localstorage);
    const [isChecked, setisChecked] = useState(true);
    const [Data, setData] = useState([]);
    // const [parentImg, setParentImg] = useState("")
    const [File_pic, setFile_pic] = useState([]);

    useEffect(() => {
        checkTokenAndRedirect(navigate);
    }, []);

    const editChange = () => {
        setisChecked(!isChecked);
    }

    const handleChange = (event) => {
        const name = event.target.name;
        const value = event.target.value;
        setData(values => ({ ...values, [name]: value }));
    }

    const imgChange = (event) => {
        // setParentImg(event.target.files[0])
        setFile_pic(URL.createObjectURL(event.target.files[0]));

    }

    useEffect(() => {
        getParent();
    }, []);

    const getParent = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `parent.php/${useToken.parent_id}/?xCase=4`);
            setData(response.data);
        } catch (error) {
            console.error(error);
        }
    };

    const handleSubmit = (event) => {
        event.preventDefault();
        var formData = new FormData();
        var img_File = document.querySelector('#img');
        // formData.append("parent_img", parentImg);
        formData.append('parent_id', useToken.parent_id);
        formData.append('parent_img', img_File.files[0]);
        formData.append('parent_cardid', Data.parent_cardid);
        formData.append('parent_prefix', Data.parent_prefix);
        formData.append('parent_firstname', Data.parent_firstname);
        formData.append('parent_lastname', Data.parent_lastname);
        formData.append('parent_address', Data.parent_address);
        formData.append('parent_moo', Data.parent_moo);
        formData.append('parent_subdis', Data.parent_subdis);
        formData.append('parent_district', Data.parent_district);
        formData.append('parent_province', Data.parent_province);
        formData.append('parent_postcode', Data.parent_postcode);
        formData.append('parent_phone', Data.parent_phone);
        formData.append('parent_line', Data.parent_line);
        formData.append('parent_email', Data.parent_email);
        formData.append('parent_career', Data.parent_career);
        formData.append('parent_salary', Data.parent_salary);
        formData.append('par_username', Data.par_username);
        formData.append('par_password', Data.par_password);
        axios.post(process.env.REACT_APP_API + `parent.php/${useToken.parent_id}/?xCase=5`, formData, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        }).then(function (response) {
            console.log(response.data);
            setisChecked(true);
            showAlertSubmit();
        });
    }

    // sweetalert2
    const showAlertSubmit = () => {
        Swal.fire({
            position: 'top-center',
            icon: 'success',
            title: 'บันทึกเสร็จสิ้น',
            showConfirmButton: false,
            timer: 2000
        })
    };

    return (
        <div>
            <Navparent />
            <section className='container'>
                <div className="card shadowcard px-lg-5">
                    <div className='fs-4 mt-3 fw-bold text-center'>{isChecked ? 'ข้อมูลส่วนตัว' : 'แก้ไขข้อมูลส่วนตัว'}</div>
                    <div className="card-body">
                        <div className="row mx-3">
                            <div className="d-flex justify-content-center">
                                <img src={File_pic == '' ? process.env.REACT_APP_API + `parents/${Data.parent_img}` : File_pic} className='rounded-circle object-fit-cover mx-auto' width={'200px'} height={'200px'} />
                            </div>
                            {isChecked ? '' :
                                <div className="col-12 my-2">
                                    <label htmlFor="" class="fs-5">รูปภาพ</label>
                                    <input class="form-control" type="file" name="parent_img" id="img" onChange={imgChange} />
                                    <input class="form-control" type="text" name="parent_id" id="img" value={Data.parent_id} hidden />
                                </div>
                            }
                            <div className="col-12 col-lg-2 my-2">
                                <label htmlFor="" className='fs-5'>คำนำหน้าชื่อ</label>
                                <input type="text" name="parent_prefix" id="parent_prefix" class="form-control" value={Data.parent_prefix} onChange={handleChange} disabled={isChecked} />
                            </div>
                            <div className="col-12 col-lg-5 my-2">
                                <label htmlFor="" className='fs-5'>ชื่อ</label>
                                <input type="text" name="parent_firstname" id="parent_firstname" class="form-control" value={Data.parent_firstname} onChange={handleChange} disabled={isChecked} />
                            </div>
                            <div className="col-12 col-lg-5 my-2">
                                <label htmlFor="" className='fs-5'>นามสกุล</label>
                                <input type="text" name="parent_lastname" id="parent_lastname" class="form-control" value={Data.parent_lastname} onChange={handleChange} disabled={isChecked} />
                            </div>
                            <div className="col-12 col-lg-6 my-2">
                                <label htmlFor="" className='fs-5'>เลขบัตรประชาชน</label>
                                <input type="text" name="parent_cardid" id="parent_cardid" class="form-control" maxLength="13" value={Data.parent_cardid} onChange={handleChange} disabled={isChecked} />
                            </div>
                            <div className="col-12 col-lg-6 my-2">
                                <label htmlFor="" className='fs-5'>เบอร์ที่ติดต่อ</label>
                                <input type="text" name="parent_phone" id="parent_phone" class="form-control" value={Data.parent_phone} onChange={handleChange} disabled={isChecked} />
                            </div>
                            <div className="col-12 col-lg-6 my-2">
                                <label htmlFor="" className='fs-5'>อีเมล</label>
                                <input type="email" name="parent_email" id="parent_email" class="form-control" value={Data.parent_email} onChange={handleChange} disabled={isChecked} />
                            </div>
                            <div className="col-12 col-lg-6 my-2">
                                <label htmlFor="" className='fs-5'>ไลน์ไอดี</label>
                                <input type="text" name="parent_line" id="parent_line" class="form-control" value={Data.parent_line} onChange={handleChange} disabled={isChecked} />
                            </div>
                            <div className="col-12 col-lg-2 my-2">
                                <label htmlFor="" className='fs-5'>ที่อยู่</label>
                                <input type="text" name="parent_address" id="parent_address" class="form-control" value={Data.parent_address} onChange={handleChange} disabled={isChecked} />
                            </div>
                            <div className="col-12 col-lg-2 my-2">
                                <label htmlFor="" className='fs-5'>หมู่</label>
                                <input type="text" name="parent_moo" id="parent_moo" class="form-control" value={Data.parent_moo} onChange={handleChange} disabled={isChecked} />
                            </div>
                            <div className="col-12 col-lg-4 my-2">
                                <label htmlFor="" className='fs-5'>ตำบล</label>
                                <input type="text" name="parent_subdis" id="parent_subdis" class="form-control" value={Data.parent_subdis} onChange={handleChange} disabled={isChecked} />
                            </div>
                            <div className="col-12 col-lg-4 my-2">
                                <label htmlFor="" className='fs-5'>อำเภอ</label>
                                <input type="text" name="parent_district" id="parent_district" class="form-control" value={Data.parent_district} onChange={handleChange} disabled={isChecked} />
                            </div>
                            <div className="col-12 col-lg-6 my-2">
                                <label htmlFor="" className='fs-5'>จังหวัด</label>
                                <input type="text" name="parent_province" id="parent_province" class="form-control" value={Data.parent_province} onChange={handleChange} disabled={isChecked} />
                            </div>
                            <div className="col-12 col-lg-6 my-2">
                                <label htmlFor="" className='fs-5'>รหัสไปรษณีย์</label>
                                <input type="text" name="parent_postcode" id="parent_postcode" class="form-control" maxLength="5" value={Data.parent_postcode} onChange={handleChange} disabled={isChecked} />
                            </div>
                            <div className="col-12 col-lg-6 my-2">
                                <label htmlFor="" className='fs-5'>อาชีพ</label>
                                <input type="text" name="parent_career" id="parent_career" class="form-control" value={Data.parent_career} onChange={handleChange} disabled={isChecked} />
                            </div>
                            <div className="col-12 col-lg-6 my-2">
                                <label htmlFor="" className='fs-5'>รายได้/ปี</label>
                                <input type="number" name="parent_salary" id="parent_salary" class="form-control" min="0" value={Data.parent_salary} onChange={handleChange} disabled={isChecked} />
                            </div>
                            <div className="col-12 col-lg-6 my-2">
                                <label htmlFor="" className='fs-5' hidden>ชื่อผู้ใช้</label>
                                <input type="text" name="par_username" id="par_username" class="form-control" value={Data.par_username} onChange={handleChange} disabled={isChecked} hidden />
                            </div>
                            <div className="col-12 col-lg-6 my-2">
                                <label htmlFor="" className='fs-5' hidden>รหัสผ่าน</label>
                                <input type="password" name="par_password" id="par_password" class="form-control" value={Data.par_password} onChange={handleChange} disabled={isChecked} hidden />
                            </div>
                            <div className="d-flex justify-content-center">
                                {isChecked ?
                                    (<div><button type='button' className='btn btn-warning mx-3 mt-3' onClick={() => editChange()}>แก้ไขข้อมูล</button> </div>)
                                    : <div><button type='button' className='btn btn-success mx-3 mt-3' onClick={handleSubmit}>บันทึกข้อมูล</button></div>
                                }
                            </div>
                        </div>

                    </div>
                </div>
            </section>
            <Footer />
        </div>
    )
}

export default Parentprofile
