import React from 'react'
import Navteacher from '../component/Navteacher'
import Footer from '../component/Footer'
import { Link, useNavigate, useParams } from 'react-router-dom'
import { useEffect, useState } from 'react'
import { checkTokenAndRedirect } from '../component/authTeacher'
import axios from 'axios'
import Swal from 'sweetalert2'

function TeacherSBeNew() {

    const navigate = useNavigate();
    const { student_id } = useParams();
    const [Data, setData] = useState([]);
    const [Data2, setData2] = useState([]);

    useEffect(() => {
        checkTokenAndRedirect(navigate);
        getStudent();
    }, []);

    const getStudent = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `student.php/${student_id}/?xCase=4`);
            setData(response.data);
        } catch (error) {
            console.error(error);
        }
    };

    const handleChange = (event) => {
        const name = event.target.name;
        const value = event.target.value;
        setData2(values => ({ ...values, [name]: value }));
    }

    const handleSubmit = (event) => {
        event.preventDefault();
        var formData = new FormData();
        formData.append('student_id', student_id);
        formData.append('b_date', Data2.b_date);
        formData.append('behaviour_details', Data2.behaviour_details);
        axios.post(process.env.REACT_APP_API + `behavior.php/?xCase=2`, formData, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        }).then(function (response) {
            console.log(response.data);
            navigate(`/TeacherBehavior/${student_id}`);
            showAlertSubmit();
        });
    }

    const showAlertSubmit = () => {
        Swal.fire({
            position: 'top-center',
            icon: 'success',
            title: 'บันทึกเสร็จสิ้น',
            showConfirmButton: false,
            timer: 2000
        })
    };

    return (
        <div>
            <Navteacher />
            <section className='h-100 d-flex flex-column justify-content-center'>
                <section className='container mt-5'>
                    <div className="card shadowcard mt-2">
                        <div className="card-body ">
                            <ol class="breadcrumb my-auto fw-bold">
                                <li class="breadcrumb-item"><Link className='text-dark' to="/TeacherHome">ชั้นเรียนที่ดูแล</Link></li>
                                <li class="breadcrumb-item"><Link className='text-dark' to={`/TeacherDataStudent/${student_id}`}>รายละเอียด เด็กชายก้านกล้วย</Link></li>
                                <li class="breadcrumb-item"><Link className='text-dark' to={`/TeacherBehavior/${student_id}`}>พฤติกรรม</Link></li>
                                <li class="breadcrumb-item active color themepink">เพิ่มรายงาน</li>
                            </ol>
                        </div>
                    </div>
                </section>
                <section className='container my-2'>
                    <div className="card shadowcard">
                        <div className="card-body m-2">
                            <form onSubmit={handleSubmit}>
                                <div className='fs-2 fw-bold text-center'>เพิ่มรายงานพฤติกรรม</div>
                                <div className="row">
                                    <div className="col-12 col-lg-6">
                                        <label for="name" class="col-form-label fw-bold">ชื่อ :</label>
                                        <input class="form-control" type="text" value={Data.student_firstname} disabled />
                                    </div>
                                    <div className="col-12 col-lg-6">
                                        <label for="name" class="col-form-label fw-bold">นามสกุล :</label>
                                        <input class="form-control" type="text" value={Data.student_lastname} disabled />
                                    </div>
                                    <div className="col-12 col-lg-6">
                                        <label for="name" class="col-form-label fw-bold">วันที่ :</label>
                                        <input class="form-control" name='b_date' type="date" id='b_date' onChange={handleChange} />
                                    </div>
                                    <div className="col-12">
                                        <label for="name" class="col-form-label fw-bold">รายละเอียด :</label>
                                        <textarea class="form-control" name='behaviour_details' rows={5} placeholder="รายละเอียด" id='behaviour_details' onChange={handleChange} />
                                    </div>
                                </div>
                                <div className='d-flex justify-content-center my-3'>
                                    <Link className='btn btn-secondary mx-2 px-5' to={`/TeacherBehavior/${student_id}`}>ยกเลิก</Link>
                                    <button className='btn btn-success mx-2 px-5' type='submit'>บันทึก</button>
                                </div>
                            </form>
                        </div>
                    </div>
                </section>
            </section>
            <Footer />
        </div>
    )
}

export default TeacherSBeNew
