import React from 'react'
import Navteacher from '../component/Navteacher'
import Footer from '../component/Footer'
import { Link, useNavigate, useParams } from 'react-router-dom'
import { useEffect, useState } from 'react'
import { checkTokenAndRedirect } from '../component/authTeacher'
import axios from 'axios'

function TeacherSBeDetail() {

    const navigate = useNavigate();
    const { student_id, behaviour_id } = useParams();
    const [Data, setData] = useState([]);
    const [Data2, setData2] = useState([]);

    useEffect(() => {
        checkTokenAndRedirect(navigate);
        getData();
        getData2();
    }, []);

    const getData = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `behavior.php/${student_id}/${behaviour_id}/?xCase=1`);
            setData(response.data);
        } catch (error) {
            console.error(error);
        }
    };

    const getData2 = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `behavior.php/${student_id}/${behaviour_id}/?xCase=3`);
            setData2(response.data);
        } catch (error) {
            console.error(error);
        }
    };

    return (
        <div>
            <Navteacher />
            <section className='h-100 d-flex flex-column justify-content-center'>
                <section className='container mt-5'>
                    <div className="card shadowcard mt-3">
                        <div className="card-body ">
                            <ol class="breadcrumb my-auto fw-bold">
                                <li class="breadcrumb-item"><Link className='text-dark' to="/TeacherHome">ชั้นเรียนที่ดูแล</Link></li>
                                <li class="breadcrumb-item"><Link className='text-dark' to={`/TeacherDataStudent/${student_id}`}>รายละเอียด เด็กชายก้านกล้วย</Link></li>
                                <li class="breadcrumb-item"><Link className='text-dark' to={`/TeacherBehavior/${student_id}`}>พฤติกรรม</Link></li>
                                <li class="breadcrumb-item active color themepink">ครั้งที่ {Data.b_odinal}</li>
                            </ol>
                        </div>
                    </div>
                </section>

                <section className='container mb-5'>
                    <div className="card shadowcard px-5">
                        <div className="card-body">
                            <div className='fs-2 fw-bold text-center'>พฤติกรรม</div>
                            <div className="d-flex justify-content-center my-3">
                                <img src={process.env.REACT_APP_API + `students/` + Data.student_img} className='rounded-circle object-fit-cover' alt="" width={"250px"} height={"250px"} />
                            </div>
                            <p className='fs-5 fw-bold text-center'>{Data.student_prefix}{Data.student_firstname} {Data.student_lastname}</p>
                            <p className='fs-5 fw-bold text-center'>ครั้งที่ {Data.b_odinal} วันที่ {Data.thai_date}</p>
                            <hr />
                            <div className='fs-5 fw-bold'>รายละเอียดพฤติกรรม</div>
                            <div className='fs-6 behaviour-descript my-3'>{Data.behaviour_details}</div>
                            <hr />
                            <div className='fs-5 fw-bold'>ข้อความจากผู้ปกครอง</div>
                            {Data2.map((Datas) => (
                                <div className="card color bgegg my-3">

                                    <div className="card-body">
                                        <p className='fs-6 fw-bold'>วันที่ {Datas.thai_dateComment}</p>
                                        <p>
                                            {Datas.comment_detail}
                                        </p>
                                    </div>
                                </div>
                            ))}

                        </div>

                    </div>
                </section>
            </section>
            <Footer />
        </div>
    )
}

export default TeacherSBeDetail
