import React from 'react'
import { Link, useNavigate, useParams } from 'react-router-dom'
import Navteacher from '../component/Navteacher'
import Footer from '../component/Footer'
import { useEffect, useState } from 'react'
import { checkTokenAndRedirect } from '../component/authTeacher'
import axios from 'axios'
import DataTable from 'react-data-table-component'

function Teacherdatastudent() {

    const { student_id } = useParams();
    const [Data, setData] = useState([]);
    const [Data2, setData2] = useState([]);
    const [Data3, setData3] = useState([]);
    const [Data4, setData4] = useState([]);
    const navigate = useNavigate();
    const [searchTerm, setSearchTerm] = useState('');
    const [searchResults, setSearchResults] = useState([]);

    useEffect(() => {
        checkTokenAndRedirect(navigate);
        getData();
        getData2();
        getData3();
        getData4();
    }, []);

    //ดึงข้อมูลนักเรียน
    const getData = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `student.php/${student_id}/?xCase=4`);
            setData(response.data);
        } catch (error) {
            console.error(error);
        }
    };

    //ดึงข้อมูลพฤติกรรมนักเรียน top 3 lates
    const getData2 = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `behavior.php/${student_id}/?xCase=0`);
            const sortedData = response.data.sort((a, b) => b.b_odinal - a.b_odinal); // Sort by b_odinal in descending order
            const top3LatestData = sortedData.slice(0, 3); // Get the top 3 records
            setData2(top3LatestData);
            // setData2(response.data);
        } catch (error) {
            console.error(error);
        }
    };

    const getData3 = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `timeinout.php/${student_id}/?xCase=4`);
            setData3(response.data);
        } catch (error) {
            console.error(error);
        }
    };

    //ดึงข้อมูลนักเรียน วันเดือนปีเกิด คำนวณอายุ และข้อมูลผู้ปกครอง
    const getData4 = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `student.php/${student_id}/?xCase=7`);
            setData4(response.data);
        } catch (error) {
            console.error(error);
        }
    };

    //ค้นหา
    const handleSearchChange = (event) => {
        const searchTerm = event.target.value;

        // ตรวจสอบว่า Data ถูกกำหนดค่าและไม่ว่าง
        if (Data3 && Data3.length > 0) {
            const filteredResults = Data3.filter((Datas) =>
                ((Datas.datethai || '').toString().toLowerCase().includes(searchTerm.toLowerCase())) ||
                (Datas.TimeIN || '').toString().toLowerCase().includes(searchTerm.toLowerCase()) ||
                (Datas.TimeOUT || '').toString().toLowerCase().includes(searchTerm.toLowerCase()) ||
                (Datas.statusHome || '').toLowerCase().includes(searchTerm.toLowerCase())
            );

            setSearchTerm(searchTerm);
            setSearchResults(filteredResults);
        } else {
            // จัดการกรณีที่ Data เป็น undefined หรือว่าง
            setSearchTerm(searchTerm);
            setSearchResults(Data3); // ใช้ Data เมื่อไม่มีคำค้นหา
        }
    };

    const customStyles = {
        rows: {
            style: {
                minHeight: '72px',
            },
        },
        headCells: {
            style: {
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                fontSize: '18px',
                fontWeight: 'bold'
            },
        },
        cells: {
            style: {
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                fontSize: '18px',
            },
        },

    };

    //ตารางการเข้าออก
    const columns = [
        {
            name: 'วันที่', selector: (row) => `${row.datethai}`, sortable: true, headerCell: () => <div className="mx-auto "></div>
        },
        { name: 'เวลาเข้า', selector: (row) => `${row.TimeIN}` },
        { name: 'เวลาออก', selector: (row) => `${row.TimeOUT}`, sortable: true },
        {
            name: 'สถานะการกลับบ้าน', selector: (row) => `${row.statusHome}`,
        },
    ];

    return (
        <div>
            <Navteacher />
            <section className='h-100 d-flex flex-column justify-content-center'>
                <section className='container mt-5'>
                    <div className="card shadowcard mt-3">
                        <div className="card-body ">
                            <ol className="breadcrumb my-auto fw-bold">
                                <li className="breadcrumb-item"><Link className='text-dark' to="/TeacherHome">ชั้นเรียนที่ดูแล</Link></li>
                                <li className="breadcrumb-item active color themepink">รายละเอียด {Data.student_prefix}{Data.student_firstname} {Data.student_lastname}</li>
                            </ol>
                        </div>
                    </div>
                </section>
                <section className='container'>

                    <div className="card shadowcard">
                        <div className='text-center fw-bold fs-4 mt-3'>
                            ข้อมูลพฤติกรรมนักเรียน
                        </div>
                        <div className="card-body">
                            <div className="row">
                                {Data2.map((Datas, key) => (
                                    <div className="col-12 col-md-6 col-lg-4 my-2">
                                        <div className="card shadowcard h-100">
                                            <div className="card-body w-100 h-100">
                                                <div className='color themepink fs-4 fw-bold'>
                                                    ครั้งที่ {Datas.b_odinal}
                                                </div>
                                                <div className='fs-5'>
                                                    วันที่ {Datas.thai_date}
                                                </div>
                                                <hr />
                                                <div className='fs-5 descript truncate-text'>
                                                    {Datas.behaviour_details}
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                ))}
                            </div>
                            <div className='d-flex justify-content-center mt-3'>
                                <Link to={`/TeacherBehavior/${student_id}`}><button className='btn btn-theme themepurple m-2 fs-5'>พฤติกรรมทั้งหมด</button></Link>
                            </div>
                        </div>
                    </div>

                </section>

                <section className='container my-4'>
                    <div className='card shadowcard'>
                        <div className='fs-4 my-3 fw-bold text-center '>การเข้าออก</div>
                        <div className="row d-flex justify-content-end">
                            <div className="col-5 col-lg-4 me-4">
                                <input type="text" className="form-control" value={searchTerm} onChange={handleSearchChange} placeholder="ค้นหา" />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12">
                                <div className='card-body'>
                                    <DataTable
                                        title=""
                                        columns={columns}
                                        data={searchTerm ? searchResults : Data3}

                                        customStyles={customStyles}
                                        pagination
                                        responsive
                                        highlightOnHover
                                        pointerOnHover
                                        noHeader
                                        className='rounded-3'
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </section>

                <section className='container my-2'>
                    <div className="card shadowcard">
                        <div className='fs-4 mt-3 fw-bold text-center'>ข้อมูลนักเรียน</div>
                        <div className="card-body">
                            <div className='d-flex justify-content-center mb-3'>
                                <img src={process.env.REACT_APP_API + `students/` + Data4.student_img} className='mx-auto rounded-circle object-fit-cover' alt="" width={"250px"} height={"250px"} />
                            </div>
                            <div className='my-4'>
                                <div className="row mx-auto justify-content-center fs-5">
                                    <div className="col-12 col-lg-5 col-md-6">
                                        <p> <strong>รหัสนักเรียน : </strong>{Data4.student_id}</p>
                                        <p> <strong>ชื่อ-นามสกุล : </strong>{Data4.student_prefix}{Data4.student_firstname} {Data4.student_lastname}</p>
                                        <p> <strong>ชื่อเล่น : </strong>{Data4.student_nickname}</p>
                                        <p> <strong>วันเดือนปีเกิด : </strong>{Data4.student_thaidate}</p>
                                        <p> <strong>น้ำหนัก : </strong>{Data4.student_weight} กิโลกรัม</p>
                                        <p> <strong>โรคประจำตัว : </strong>{Data4.student_congenitalDisease || '-'}</p>
                                    </div>
                                    <div className="col-12 col-lg-5 col-md-6">
                                        <p> <strong>เลขบัตรประชาชน : </strong>{Data4.student_cardid}</p>
                                        <p> <strong>ชื่อ-นามสกุล (English) : </strong>{Data4.student_engname}</p>
                                        <p> <strong>ชั้น : </strong>{Data4.class_name}{Data4.classroom_name}</p>
                                        <p> <strong>อายุ : </strong>{Data4.student_age} ปี</p>
                                        <p> <strong>ส่วนสูง : </strong>{Data4.student_height} เซนติเมตร</p>
                                        <p> <strong>ที่อยู่ : </strong>{Data4.student_address}{Data4.student_moo} หมู่ {Data4.student_moo} ตำบล{Data4.student_subdis} อำเภอ{Data4.student_district} จังหวัด{Data4.student_province} {Data4.student_postcode}</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                <section className='container my-3'>
                    <div className="card shadowcard">
                        <div className='fs-4 mt-3 fw-bold text-center'>ข้อมูลผู้ปกครอง</div>
                        <div className="card-body">
                            <div className='d-flex justify-content-center mb-3'>
                                <img src={process.env.REACT_APP_API + `parents/` + Data4.parent_img} className='mx-auto rounded-circle object-fit-cover' alt="" width={"250px"} height={"250px"} />
                            </div>
                            <div className="my-4">
                                <div className="row mx-auto justify-content-center fs-6">
                                    <div className="col-lg-5">
                                        <p> <strong>ชื่อ-นามสกุล : </strong>{Data4.parent_prefix}{Data4.parent_firstname} {Data4.parent_lastname}</p>
                                        <p> <strong>อาชีพ : </strong>{Data4.parent_career}</p>
                                        <p> <strong>ที่อยู่ : </strong>{Data4.parent_address} หมู่ {Data4.parent_moo} ตำบล{Data4.parent_subdis} อำเภอ{Data4.parent_district} จังหวัด{Data4.parent_province} {Data4.parent_postcode}</p>
                                        <p> <strong>ไลน์ไอดี : </strong>{Data4.parent_line}</p>
                                    </div>
                                    <div className="col-lg-5">
                                        <p> <strong>เลขบัตรประชาชน : </strong>{Data4.parent_cardid}</p>
                                        <p> <strong>รายได้/ปี : </strong>{Data4.parent_salary} บาท</p>
                                        <p> <strong>เบอร์โทรศัพท์ : </strong>{Data4.parent_phone}</p>
                                        <p> <strong>อีเมล : </strong>{Data4.parent_email || '-'}</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </section>
            <Footer />
        </div>
    )
}

export default Teacherdatastudent
