import React from 'react'
import { Link, useNavigate } from 'react-router-dom'
import axios from "axios"
import Navteacher from '../component/Navteacher'
import Footer from '../component/Footer'
import { useEffect, useState } from 'react'
import { checkTokenAndRedirect } from '../component/authTeacher'

function Parent() {
    const localstorage = localStorage.getItem('TokenTeacher');
    const useToken = JSON.parse(localstorage);
    //เก็บข้อมูลนักเรียน
    const [Data, setData] = useState([]);
    const [Data2, setData2] = useState([]);
    const navigate = useNavigate();

    useEffect(() => {
        getData();
        getData2();
    }, []);

    //ดึงข้อมูลนักเรียน
    const getData = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `student.php/?xCase=0`);
            setData(response.data);
        } catch (error) {
            console.error(error);
        }
    };

    //ดึงข้อมูลจำนวนครั้งในการเข้าเรียนของนักเรียน
    const getData2 = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `timeinout.php/${useToken.classroom_id}/?xCase=3`);
            setData2(response.data);
        } catch (error) {
            console.error(error);
        }
    };

    const filterConditions = {
        // class_id: "1",
        classroom_id: useToken.classroom_id,
    };

    const filteredInclass = Data.filter((student) => {
        // const isClassValid = student.class_id == filterConditions.class_id;
        const isClassroomValid = student.classroom_id == filterConditions.classroom_id;
        return isClassroomValid;
    });

    filteredInclass.sort((a, b) => a.student_id.localeCompare(b.student_id));

    const filteredMaleStudents = Data.filter((student) => {
        const isPrefixValid = student.student_prefix.includes("เด็กชาย");
        // const isClassValid = student.class_id == filterConditions.class_id;
        const isClassroomValid = student.classroom_id == filterConditions.classroom_id;
        // return isPrefixValid && isClassValid && isClassroomValid;
        return isPrefixValid && isClassroomValid;
    });

    const filteredFemaleStudents = Data.filter((student) => {
        const isPrefixValid = student.student_prefix.includes("เด็กหญิง");
        // const isClassValid = student.class_id == filterConditions.class_id;
        const isClassroomValid = student.classroom_id == filterConditions.classroom_id;
        // return isPrefixValid && isClassValid && isClassroomValid;
        return isPrefixValid && isClassroomValid;
    });

    useEffect(() => {
        checkTokenAndRedirect(navigate);
    }, []);

    return (
        <div>
            <Navteacher />
            <section className="h-100 d-flex flex-column justify-content-center banner">
                <div className="text-white text-uppercase fw-bold text-center my-auto mt-3">
                    <div className='fs-1 text-center color themepink fw-bold my-5'>นักเรียนชั้นประถมศึกษาปีที่ 3</div>
                    <div className='container'>
                        <div className='row'>
                            <div className='col-12 col-lg-6 mx-auto'>
                                <div className="row d-flex flex-column">
                                    <div class="col-12 fs-1">
                                        <div class="circle-teacher d-flex justify-content-center">
                                            <div class="circle-teacher__inner">
                                                <div class="circle-teacher__wrapper">
                                                    <div class="circle-teacher__content color themeblue">{filteredMaleStudents.length}</div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="fs-4 col-12 mx-auto color themeblue my-3">

                                        จำนวนนักเรียนชาย
                                        <i class="fa-solid fa-person  fs-1 ms-3"></i>
                                    </div>
                                </div>
                            </div>
                            <div className='col-12 col-lg-6 mx-auto'>
                                <div className="row d-flex flex-column">
                                    <div class="col-12 fs-1">
                                        <div class="circle-teacher d-flex justify-content-center">
                                            <div class="circle-teacher__inner">
                                                <div class="circle-teacher__wrapper">
                                                    <div class="circle-teacher__content color themepink">{filteredFemaleStudents.length}</div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="fs-4 col-12 mx-auto color themepink my-3">
                                        จำนวนนักเรียนหญิง
                                        <i class="fa-solid fa-person-dress fs-1 ms-3"></i>
                                    </div>

                                </div>
                            </div>
                            <div className='col-12 col-lg-6 mx-auto'>
                                <div className="row d-flex flex-column">
                                    <div class="col-12 fs-1">
                                        <div class="circle-teacher d-flex justify-content-center">
                                            <div class="circle-teacher__inner">
                                                <div class="circle-teacher__wrapper">
                                                    <div class="circle-teacher__content color theme">{filteredInclass.length}</div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="fs-4 col-12 mx-auto color theme my-3">
                                        จำนวนนักเรียนทั้งหมดในห้อง
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

            <section className='container my-5'>
                <div className='row'>
                    {filteredInclass.map((filteredInclass) => (
                        <div className='col-12 col-md-4 col-lg-3 mx-auto my-3'>
                            <div class="mx-auto">
                                <Link className='text-dark' to={`/TeacherDataStudent/${filteredInclass.student_id}`}>
                                    <div className="circle-student d-flex justify-content-center">
                                        <div className='circle-student__inner'>
                                            <img src={process.env.REACT_APP_API + `students/` + filteredInclass.student_img} className='rounded-circle circle-student__wrapper object-fit-cover' alt="" />
                                        </div>
                                    </div>
                                    <div className='fs-6 mx-auto fw-bold text-center mt-2'>{filteredInclass.student_id}</div>
                                    <div className='fs-6 mx-auto fw-bold text-center'>{filteredInclass.student_prefix}{filteredInclass.student_firstname} {filteredInclass.student_lastname}</div>
                                    <div className='fs-6 mx-auto fw-bold text-center'>{filteredInclass.student_nickname}</div>
                                </Link>
                            </div>
                        </div>
                    ))}
                </div>
            </section>

            <section className='container mb-5'>
                <div className='card shadowcard p-3'>
                    <div className='fs-3 mt-2 fw-bold text-center'>การเข้าโรงเรียน</div>
                    <div className='m-1'>
                        <table className='text-center fs-6 w-100 table table-striped table-hover'>
                            <thead>
                                <tr>
                                    <th scope="col">รหัสนักเรียน</th>
                                    <th scope="col">ชื่อนักเรียน</th>
                                    <th scope="col">ชื่อเล่น</th>
                                    <th scope="col">จำนวนครั้ง</th>
                                </tr>
                            </thead>
                            <tbody>
                                {Data2.map((Datas) => (
                                    <tr>
                                        <td scope="row">{Datas.student_id}</td>
                                        <td>{Datas.student_prefix}{Datas.student_firstname} {Datas.student_lastname}</td>
                                        <td>{Datas.student_nickname}</td>
                                        <td>{Datas.stin}</td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                </div>
            </section>
            <Footer />
        </div>
    )
}

export default Parent
