import React from 'react'
import './teacher.css'
import { Link, useNavigate } from "react-router-dom"
import Swal from 'sweetalert2'
import { useEffect, useState } from "react"
import axios from "axios"

function TeacherLog() {

    const navigate = useNavigate();
    const [inputs, setInputs] = useState([]);

    const storedToken = localStorage.getItem('TokenTeacher');


    useEffect(() => {
        if (storedToken !== null) {
            navigate('/TeacherHome');
            Swal.fire({
                icon: 'error',
                title: 'เกิดข้อผิดพลาด',
                text: 'ทำการล็อกอินเรียบร้อยแล้ว หากต้องการออกจากระบบกรุณาทำการออกจากระบบก่อนทุกครั้ง',
                showConfirmButton: false,
                timer: 5000,
                customClass: {
                    container: 'white-bg',
                },
                background: 'white',
            })
        }
    }, []);

    const handleChange = (event) => {
        const name = event.target.name;
        const value = event.target.value;
        setInputs(values => ({ ...values, [name]: value }));
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        var formData = new FormData();
        formData.append('teachlog_username', inputs.teachlog_username);
        formData.append('teachlog_password', inputs.teachlog_password);
        axios.post(process.env.REACT_APP_API + `LoginTeacher.php?xCase=0`, formData, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        }).then((result) => {
            console.log(result.data);
            if (result.data.status === 1 && result.data.classroom_id != null) {
                localStorage.setItem('TokenTeacher', JSON.stringify(result.data));
                Swal.fire({
                    icon: 'success',
                    title: 'เข้าระบบสำเร็จ',
                    text: 'ทำการเข้าสู่ระบบสำเร็จ',
                    customClass: {
                        popup: 'swal',
                    },
                    showConfirmButton: false,
                    timer: 2500,
                }).then(function () {
                    navigate(`/TeacherHome`);
                })

            }
            else if (result.data.status === 1 && result.data.classroom_id === null) {
                localStorage.setItem('TokenTeacher', JSON.stringify(result.data));
                Swal.fire({
                    icon: 'success',
                    title: 'เข้าระบบสำเร็จ',
                    text: 'ทำการเข้าสู่ระบบสำเร็จ',
                    customClass: {
                        popup: 'swal',
                    },
                    showConfirmButton: false,
                    timer: 2500,
                }).then(function () {
                    navigate(`/TeacherHomework`);
                })

            }
            else {
                Swal.fire({
                    icon: 'error',
                    title: 'เข้าระบบไม่สำเร็จ',
                    text: 'ชื่อผู้ใช้หรือรหัสผ่านไม่ถูกต้อง กรุณาตรวจสอบอีกครั้ง',
                    customClass: {
                        popup: 'swal',
                    },
                    showConfirmButton: false,
                    timer: 2500,
                })
            }
        })
    };

    return (
        <div>
            <div class="h-100">
                <div class="row d-flex justify-content-center align-items-center h-100">
                    <div class="col col-xl-12">
                        <div class="">
                            <div class="row g-0">
                                <div class="col-md-6 d-none d-md-block">
                                    <Link to="/Login">
                                        <h5 className='text-white fw-bold fixed-top m-3'>
                                            {`<`} กลับ
                                        </h5>
                                    </Link>
                                    <img src={process.env.REACT_APP_PATH + `/Image/walllog.png`}
                                        alt="login form" class="img-fluid object-fit-cover h-100" />

                                </div>
                                <div class="col-md-6 d-flex align-items-center">
                                    <div class="w-100 p-5 p-lg-5 text-black">
                                        <form onSubmit={handleSubmit}>
                                            <div className='text-center'>
                                                <div class="fs-1 fw-bold my-3" >ลงชื่อเข้าใช้</div>
                                                <div class="fs-4 fw-bold mb-4" >สำหรับ<span className='color theme'>คุณครู</span>โรงเรียนบ้านน้ำจำ</div>
                                            </div>

                                            <div class="form-group position-relative my-3 in-shadow">
                                                <input type="text" class="form-control is-valid p-2" placeholder='ป้อนชื่อผู้ใช้' id="teachlog_username" name='teachlog_username' onChange={handleChange} />
                                            </div>
                                            <div class="form-group position-relative my-3 in-shadow">
                                                <input type="password" class="form-control is-valid p-2" placeholder='ป้อนรหัสผ่าน' id="teachlog_password" name='teachlog_password' onChange={handleChange} />
                                            </div>

                                            <div class="my-4">
                                                <button class="btn btn-theme btn-lg btn-block w-100 " type="submit">เข้าสู่ระบบ</button>
                                            </div>
                                        </form>

                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </div>
    )
}

export default TeacherLog
