import { React, useState } from 'react'
import './nav.css'
import { Link, useLocation, useParams } from 'react-router-dom'

function Navparent() {
    const location = useLocation();
    const localstorage = localStorage.getItem('TokenTeacher');
    const useToken = JSON.parse(localstorage);
    const { student_id, behaviour_id, subject_id, subject_name, classroom_id, teaching_id, homework_id } = useParams();
    const encodedSubjectName = encodeURIComponent(subject_name);
    const [showSidebar, setShowSidebar] = useState(false);
    const toggleSidebar = () => {
        setShowSidebar(!showSidebar);
    };

    return (
        <nav className="navbar navbar-expand-lg navbar-dark navbar-shadow position-absolute w-100 ">
            <div className="container-fluid">
                <Link className="navbar-brand fs-4 color-theme" to={useToken.classroom_id != null ? `/TeacherHome` : `/TeacherHomework`}><img src={process.env.REACT_APP_PATH + `Image/logo.png`} width="50" height="50" alt="" />โรงเรียนบ้านน้ำจำ</Link>
                <ul className="navbar-nav justify-content-center align-items-center fs-5 flex-grow-1 pe-3 d-none d-lg-inline-flex color-theme">
                    {useToken.classroom_id != null ? <li className="nav-item">
                        <Link to="/TeacherHome" className={`nav-link text-center ${(location.pathname === `/TeacherHome` || location.pathname == `/TeacherDataStudent/${student_id}` || location.pathname === `/TeacherBehavior/${student_id}` || location.pathname === `/TeacherBehavior/${student_id}/new` || location.pathname === `/TeacherBehaviorDetail/${student_id}/${behaviour_id}`) ? 'active-n' : ''}`} >ชั้นเรียนที่ดูแล</Link>
                    </li> : ""}
                    <li className="nav-item">
                        <Link to="/TeacherHomework" className={`nav-link text-center me-5 ${(location.pathname === `/TeacherHomework` || location.pathname === `/TeacherHomework/${subject_id}/${encodedSubjectName}` || location.pathname === `/TeacherHomework/${subject_id}/${encodedSubjectName}/${classroom_id}/${teaching_id}` || location.pathname === `/TeacherHomework/${subject_id}/${encodedSubjectName}/${classroom_id}/${teaching_id}/${homework_id}/HWdetail`) ? 'active-n' : ''}`}>การบ้าน</Link>

                    </li>
                </ul>
                <div class="dropdown">
                    <Link to="" className='text-decoration-none dropdown-toggle d-none d-lg-inline' data-bs-toggle="dropdown" aria-expanded="false"><i className="fa-regular fa-circle-user nav-icon"></i></Link>
                    <ul class="dropdown-menu text-center">
                        <li><Link to="/TeacherProfile" class={`dropdown-item fs-6 ${(location.pathname === `/TeacherProfile` || location.pathname === `/TeacherProfile/edit`) ? 'active-slide' : ''}`}>ข้อมูลส่วนตัว</Link></li>
                        <li><Link to="/Login" class="dropdown-item fs-6" onClick={() => { localStorage.removeItem('TokenTeacher') }}>ออกจากระบบ</Link></li>
                    </ul>
                </div>
                {/* overlay */}
                <div className={`overlay ${showSidebar ? 'show' : ''}`} onClick={toggleSidebar}></div>
                {/* mobile */}
                <button className="navbar-toggler shadow-none border-0" type="button" onClick={toggleSidebar}>
                    <i class="fa-solid fa-bars fs-2 color-theme"></i>
                </button>
                <div className={`sidebar ${showSidebar ? 'show' : ''} d-block d-lg-none`}>
                    <div className='d-flex justify-content-end'>
                        <button type="button" className="btn-close shadow-none color-theme" onClick={toggleSidebar}></button>
                    </div>
                    <div className='d-flex justify-content-center'>
                        <ul className="navbar-nav fs-5 d-inline-flex d-lg-none">
                            <li className="nav-item">
                                <Link to="/TeacherHome" className={`nav-link ${(location.pathname === `/TeacherHome` || location.pathname == `/TeacherDataStudent/${student_id}` || location.pathname === `/TeacherBehavior/${student_id}` || location.pathname === `/TeacherBehavior/${student_id}/new` || location.pathname === `/TeacherBehaviorDetail/${student_id}/${behaviour_id}`) ? 'active-slide' : ''} align-items-center d-flex`} >
                                    <i class="fa-solid fa-screen-users fs-2 me-2"></i>ชั้นเรียนที่ดูแล</Link>
                            </li>
                            <li className="nav-item">
                                <Link to="/TeacherHomework" className={`nav-link ${(location.pathname === `/TeacherHomework` || location.pathname === `/TeacherHomework/${subject_id}/${encodedSubjectName}` || location.pathname === `/TeacherHomework/${subject_id}/${encodedSubjectName}/${classroom_id}/${teaching_id}` || location.pathname === `/TeacherHomework/classroom/homework` || location.pathname === `TeacherHomework/${subject_id}/${encodedSubjectName}/${classroom_id}/${teaching_id}/${homework_id}/HWdetail`) ? 'active-slide' : ''} align-items-center d-flex`}>
                                    {/* <i class="fa-regular fa-book-open-cover fs-2 me-2"></i>การบ้าน</Link> */}
                                    <i class="fa-regular fa-book-copy fs-2 me-2"></i>การบ้าน</Link>
                            </li>
                            <li className="nav-item">
                                <Link to="/TeacherProfile" className={`nav-link ${(location.pathname === `/TeacherProfile`) ? 'active-slide' : ''} align-items-center d-flex`}>
                                    <i class="fa-solid fa-circle-user fs-1 me-2"></i>ข้อมูลส่วนตัว</Link>
                            </li>
                        </ul>

                    </div>
                    <div className="d-flex justify-content-center">
                        <ul className="navbar-nav fs-5 d-lg-none">
                            <li className="nav-item">
                                <Link to="/Login" className={`nav-link lower-menu-item align-items-center d-flex`} onClick={() => { localStorage.removeItem('TokenTeacher') }}>
                                    <i class="fa-duotone fa-arrow-right-from-bracket fs-2 me-2"></i>ออกจากระบบ
                                </Link>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </nav >
    )
}

export default Navparent
