import { BrowserRouter, Routes, Route } from "react-router-dom"
import './App.css'
import Home from "./Home"
import Login from "./Login"
import TeacherLog from "./teacher/teacherLog"
import TeacherHome from "./teacher/teacherHome"
import TeacherHomework from "./teacher/teacherHomework"
import TeacherHWClass from "./teacher/teacherHWClass"
import TeacherDataStudent from "./teacher/teacherdatastudent"
import TeacherBehavior from "./teacher/teacherBehavior"
import TeacherSBeNew from "./teacher/teacherSBeNew"
import TeacherSBeDetail from "./teacher/teacherSBeDetail"
import TeacherHWCroom from "./teacher/teacherHWCroom"
// import TeacherHWChomework from "./teacher/teacherHWChomework"
import TeacherHWCHWDeatail from "./teacher/teacherHWCHWDeatail"
// import TeacherHWCnewhomework from "./teacher/teacherHWCnewhomework"
import TeacherProfile from "./teacher/teacherProfile"
import TeacherProfileedit from "./teacher/teacherProfileedit"

import ParentLog from "./parent/parentLog"
import ParentHome from "./parent/parentHome"
import ParentStudent from "./parent/parentStudent"
import ParentHomework from "./parent/parentHomework"
import ParentHomeworkdetail from "./parent/parentHomeworkdetail"
import ParentBehavior from "./parent/parentBehavior"
import ParentBehaviordetail from "./parent/parentBehaviordetail"
import Parentprofile from "./parent/parentprofile"
import Parentprofileedit from "./parent/parentprofileedit"


function App() {

  return (
    <BrowserRouter basename="/BanNamJam_School">
      <Routes>
        <Route index element={<Home />} />
        <Route path="Login" element={<Login />} />
        <Route path="TeacherLog" element={<TeacherLog />} />
        <Route path="TeacherHome" element={<TeacherHome />} />
        <Route path="TeacherHomework" element={<TeacherHomework />} />
        <Route path="TeacherHomework/:subject_id/:subject_name" element={<TeacherHWClass />} />
        <Route path="TeacherHomework/:subject_id/:subject_name/:classroom_id/:teaching_id" element={<TeacherHWCroom />} />
        {/* <Route path="TeacherHomework/classroom/homework" element={<TeacherHWChomework />} /> */}
        <Route path="TeacherHomework/:subject_id/:subject_name/:classroom_id/:teaching_id/:homework_id/HWdetail" element={<TeacherHWCHWDeatail />} />
        {/* <Route path="TeacherHomework/classroom/newhomework" element={<TeacherHWCnewhomework />} /> */}
        <Route path="TeacherProfile" element={<TeacherProfile />} />
        <Route path="TeacherProfile/edit" element={<TeacherProfileedit />} />
        <Route path="TeacherDataStudent/:student_id" element={<TeacherDataStudent />} />
        <Route path="TeacherBehavior/:student_id" element={<TeacherBehavior />} />
        <Route path="TeacherBehavior/:student_id/new" element={<TeacherSBeNew />} />
        <Route path="TeacherBehaviorDetail/:student_id/:behaviour_id" element={<TeacherSBeDetail />} />

        <Route path="ParentLog" element={<ParentLog />} />
        <Route path="ParentHome" element={<ParentHome />} />
        <Route path="ParentStudent/:student_id" element={<ParentStudent />} />
        <Route path="ParentHomework/:student_id" element={<ParentHomework />} />
        <Route path="ParentHomework/:student_id/:teaching_id/detail" element={<ParentHomeworkdetail />} />
        <Route path="ParentBehavior/:student_id" element={<ParentBehavior />} />
        <Route path="ParentBehavior/:student_id/:behaviour_id/detail" element={<ParentBehaviordetail />} />
        <Route path="Parentprofile" element={<Parentprofile />} />
        <Route path="Parentprofile/edit" element={<Parentprofileedit />} />
      </Routes>
    </BrowserRouter>
  );
}

export default App;
