import Swal from 'sweetalert2';

export function checkTokenAndRedirect(navigate) {
    const storedToken = localStorage.getItem('TokenTeacher');
    if (storedToken === null) {
        navigate('/TeacherLog');
        Swal.fire({
            icon: 'error',
            title: 'เกิดข้อผิดพลาด',
            text: 'กรุณาล็อคอิน',
            showConfirmButton: false,
            timer: 2500,
            customClass: {
                container: 'white-bg',
            },
            background: 'white',
        })
    }
}