import React from 'react'
import { Link, useNavigate, useParams } from 'react-router-dom'
import Navteacher from '../component/Navteacher'
import Footer from '../component/Footer'
import { useEffect, useState } from 'react'
import { checkTokenAndRedirect } from '../component/authTeacher'
import axios from 'axios'

function TeacherHWClass() {
    const { subject_id, subject_name } = useParams();
    const navigate = useNavigate();
    const localstorage = localStorage.getItem('TokenTeacher');
    const useToken = JSON.parse(localstorage);
    const [Data, setData] = useState([]);

    useEffect(() => {
        checkTokenAndRedirect(navigate);
        getData();
    }, []);

    const getData = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `teaching.php/${useToken.teacher_id}/${subject_id}/?xCase=13`);
            setData(response.data);
            console.log(response.data)
        } catch (error) {
            console.error(error);
        }
    };


    return (
        <div>
            <Navteacher />
            <section className='h-100 d-flex flex-column justify-content-center'>
                <section className='container mt-5'>
                    <div className="card shadowcard mt-3">
                        <div className="card-body ">
                            <ol class="breadcrumb my-auto fw-bold">
                                <li class="breadcrumb-item"><Link className='text-dark' to="/TeacherHomework">การบ้าน</Link></li>
                                <li class="breadcrumb-item active color themepink">วิชา{subject_name}</li>
                            </ol>
                        </div>
                    </div>
                </section>

                <section className='container color theme'>
                    <div className='fs-1 fw-bold text-center '>
                        การบ้านวิชา
                    </div>
                    <div className='fs-5 fw-bold text-center'>
                        เลือกวิชา
                    </div>
                    <div className="row my-4 justify-content-center">
                        {/* Start content */}
                        {Data.map((Datas, key) => (
                            <div className='col-12 col-md-4 col-lg-4 my-1'>
                                <Link to={`/TeacherHomework/${Datas.subject_id}/${Datas.subject_name}/${Datas.classroom_id}/${Datas.teaching_id}`}>
                                    <div className="row d-flex flex-column">
                                        <div class="col-12 fs-4 fw-bold">
                                            <div class="circle-homework d-flex justify-content-center">
                                                <div class="circle-homework__inner">
                                                    <div class="circle-homework__wrapper">
                                                        <div class="circle-homework__content text-white fs-1">ป.{Datas.class_id}/{Datas.classroom_name.slice(5, 6)}</div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="fs-5 col-12 mx-auto fw-bold color theme my-3 text-center">
                                            {/* <div>การบ้าน : แต่งประโยค</div> */}
                                            <div>การบ้านล่าสุด : </div>
                                        </div>
                                    </div>
                                </Link>
                            </div>
                        ))}
                        {/* End content */}

                    </div>
                </section>
            </section>

            <Footer />
        </div>
    )
}

export default TeacherHWClass
