import React from 'react'
import { Link, useNavigate, useParams } from 'react-router-dom'
import Navparent from '../component/Navparent'
import Footer from '../component/Footer'
import { useEffect, useState } from 'react'
import { checkTokenAndRedirect } from '../component/authParent'
import axios from 'axios'
import DataTable from 'react-data-table-component'

function ParentStudent() {

    const navigate = useNavigate();
    const localstorage = localStorage.getItem('TokenParent');
    const useToken = JSON.parse(localstorage);
    const [Data, setData] = useState([]);
    const [Data2, setData2] = useState([]);
    const { student_id } = useParams();
    const [searchTerm, setSearchTerm] = useState('');
    const [searchResults, setSearchResults] = useState([]);

    useEffect(() => {
        checkTokenAndRedirect(navigate);
        getData();
        getData2();
    }, []);

    const getData = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `student.php/${useToken.parent_id}/${student_id}/?xCase=14`);
            setData(response.data);
        } catch (error) {
            console.error(error);
        }
    };

    const getData2 = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `timeinout.php/${useToken.parent_id}/${student_id}/?xCase=5`);
            setData2(response.data);
        } catch (error) {
            console.error(error);
        }
    };

    const handleSearchChange = (event) => {
        const searchTerm = event.target.value;

        // ตรวจสอบว่า Data ถูกกำหนดค่าและไม่ว่าง
        if (Data2WithDefault && Data2WithDefault.length > 0) {
            const filteredResults = Data2WithDefault.filter((Datas) =>
                ((Datas.datethai || '').toString().toLowerCase().includes(searchTerm.toLowerCase())) ||
                (Datas.TimeIN || '').toString().toLowerCase().includes(searchTerm.toLowerCase()) ||
                (Datas.TimeOUT || '').toString().toLowerCase().includes(searchTerm.toLowerCase()) ||
                (Datas.statusHome || '').toLowerCase().includes(searchTerm.toLowerCase())
            );

            setSearchTerm(searchTerm);
            setSearchResults(filteredResults);
        } else {
            // จัดการกรณีที่ Data เป็น undefined หรือว่าง
            setSearchTerm(searchTerm);
            setSearchResults(Data2WithDefault); // ใช้ Data เมื่อไม่มีคำค้นหา
        }
    };

    const customStyles = {
        rows: {
            style: {
                minHeight: '72px',
            },
        },
        headCells: {
            style: {
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                fontSize: '18px',
                fontWeight: 'bold'
            },
        },
        cells: {
            style: {
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                fontSize: '18px',
            },
        },

    };
    // แปลงค่า null เป็น -
    const Data2WithDefault = Data2.map(row => ({
        ...row,
        datethai: row.datethai || '-',
        TimeIN: row.TimeIN || '-',
        TimeOUT: row.TimeOUT || '-',
        statusHome: row.statusHome || '-',
    }));

    //ตารางการเข้าออก
    const columns = [
        {
            name: 'วันที่', selector: (row) => `${row.datethai}`, sortable: true, headerCell: () => <div className="mx-auto "></div>
        },
        { name: 'เวลาเข้า', selector: (row) => `${row.TimeIN}`, sortable: true },
        { name: 'เวลาออก', selector: (row) => `${row.TimeOUT}`, sortable: true },
        {
            name: 'สถานะการกลับบ้าน', selector: (row) => `${row.statusHome}`, sortable: true,
        },
    ];

    return (
        <div>
            <Navparent />
            <section className='container'>
                <div className="card shadowcard">
                    <div className="card-body ">
                        <ol class="breadcrumb my-auto fw-bold">
                            <li class="breadcrumb-item"><Link className='text-dark' to="/ParentHome">หน้าหลัก</Link></li>
                            <li class="breadcrumb-item active color themepink">ข้อมูล {Data.student_prefix}{Data.student_firstname} {Data.student_lastname}</li>
                        </ol>
                    </div>
                </div>
            </section>
            <section className='container'>
                <div className="card shadowcard">
                    <div className='fs-4 mt-3 fw-bold text-center'>ข้อมูลส่วนตัว</div>
                    <div className="card-body">
                        <div className='d-flex justify-content-center mb-3'>
                            <img src={process.env.REACT_APP_API + `students/` + Data.student_img} className='mx-auto rounded-circle object-fit-cover' alt="" width={"250px"} height={"250px"} />
                        </div>
                        <div className="row mx-auto justify-content-center fs-5">
                            <div className="col-lg-5">
                                <p> <strong>รหัสนักเรียน : </strong>{Data.student_id}</p>
                                <p> <strong>ชื่อ-นามสกุล : </strong>{Data.student_prefix}{Data.student_firstname} {Data.student_lastname}</p>
                                <p> <strong>ชื่อเล่น : </strong>{Data.student_nickname}</p>
                                <p> <strong>วันเดือนปีเกิด : </strong>{Data.student_thaidate}</p>
                                <p> <strong>น้ำหนัก : </strong>{Data.student_weight} กิโลกรัม</p>
                                <p> <strong>โรคประจำตัว : </strong>{Data.student_congenitalDisease ? Data.student_congenitalDisease : '-'}</p>
                            </div>
                            <div className="col-lg-5">
                                <p> <strong>เลขบัตรประชาชน : </strong>{Data.student_cardid}</p>
                                <p> <strong>ชื่อ-นามสกุล (English) : </strong>{Data.student_engname}</p>
                                <p> <strong>ชั้น : </strong>{Data.class_name} {Data.classroom_name}</p>
                                <p> <strong>อายุ : </strong>{Data.student_age} ปี</p>
                                <p> <strong>ส่วนสูง : </strong>{Data.student_height} เซนติเมตร</p>
                                <p> <strong>ที่อยู่ : </strong>{Data.student_address} หมู่ {Data.student_moo} ตำบล{Data.student_subdis} อำเภอ{Data.student_district} จังหวัด{Data.student_province} {Data.student_postcode}</p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <section className='container my-2'>
                <div className='card shadowcard'>
                    <div className='fs-4 mt-2 fw-bold text-center'>ข้อมูลการเข้า-ออกโรงเรียน</div>
                    <div className="row d-flex justify-content-end">
                        <div className="col-5 col-lg-4 me-4">
                            <input type="text" className="form-control" value={searchTerm} onChange={handleSearchChange} placeholder="ค้นหา" />
                        </div>
                    </div>
                    <div className='m-3'>
                        <DataTable
                            title=""
                            columns={columns}
                            data={searchTerm ? searchResults : Data2WithDefault}
                            customStyles={customStyles}
                            pagination
                            responsive
                            highlightOnHover
                            pointerOnHover
                            noHeader
                            className='rounded-3'
                        />
                    </div>
                </div>
            </section>
            <Footer />
        </div>
    )
}

export default ParentStudent
