import React from 'react'
import './parent.css'
import { Link, useNavigate } from 'react-router-dom'
import Swal from 'sweetalert2'
import { useEffect, useState } from "react"
import axios from "axios"

function ParentLog() {

    const navigate = useNavigate();
    const [inputs, setInputs] = useState([]);

    const storedToken = localStorage.getItem('TokenParent');


    useEffect(() => {
        if (storedToken !== null) {
            navigate('/ParentHome');
            Swal.fire({
                icon: 'error',
                title: 'เกิดข้อผิดพลาด',
                text: 'ทำการล็อกอินเรียบร้อยแล้ว หากต้องการออกจากระบบกรุณาทำการออกจากระบบก่อนทุกครั้ง',
                showConfirmButton: false,
                timer: 5000,
                customClass: {
                    container: 'white-bg',
                },
                background: 'white',
            })
        }
    }, []);

    const handleChange = (event) => {
        const name = event.target.name;
        const value = event.target.value;
        setInputs(values => ({ ...values, [name]: value }));
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        var formData = new FormData();
        formData.append('par_username', inputs.par_username);
        formData.append('par_password', inputs.par_password);
        axios.post(process.env.REACT_APP_API + `LoginParent.php?xCase=0`, formData, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        }).then((result) => {
            console.log(result.data);
            if (result.data.status === 1) {
                localStorage.setItem('TokenParent', JSON.stringify(result.data));
                Swal.fire({
                    icon: 'success',
                    title: 'เข้าสู่ระบบสำเร็จ',
                    text: 'ทำการล็อกอินเรียบร้อยแล้ว',
                    customClass: {
                        popup: 'swal',
                    },
                    showConfirmButton: false,
                    timer: 2500,
                }).then(function () {
                    navigate(`/ParentHome`);
                })

            }
            else {
                Swal.fire({
                    icon: 'error',
                    title: 'เข้าระบบไม่สำเร็จ',
                    text: 'ชื่อผู้ใช้หรือรหัสผ่านไม่ถูกต้อง กรุณาตรวจสอบอีกครั้ง',
                    customClass: {
                        popup: 'swal',
                    },
                    showConfirmButton: false,
                    timer: 2500,
                })
            }
        })
    };

    return (
        <div>
            <div class="row d-flex justify-content-center align-items-center h-100">
                <div class="col col-xl-12">
                    <div class="">
                        <div class="row g-0">
                            <div class="col-md-6 d-none d-md-block">
                                <Link to="/Login">
                                    <div className='text-white fw-bold fixed-top fs-5 m-3'>
                                        {`<`} กลับ
                                    </div>
                                </Link>
                                <img src={process.env.REACT_APP_PATH + `/Image/walllog2.png`}
                                    alt="login form" class="img-fluid object-fit-cover h-100" />

                            </div>
                            <div class="col-md-6 d-flex align-items-center">
                                <div class="w-100 p-4 p-lg-5 text-black">
                                    <form onSubmit={handleSubmit}>
                                        <div className='text-center'>
                                            <div class="fs-1 fw-bold mb-0 pb-3" >ลงชื่อเข้าใช้</div>
                                            <div class="fs-5 fw-bold mb-3 pb-3" >สำหรับ<span className='color theme'>ผู้ปกครอง</span>โรงเรียนบ้านน้ำจำ</div>
                                        </div>
                                        {/* <div class="form-outline mb-4">
                                                <div class="input-group mb-3 in-shadow">
                                                    <input type="email" id="form2Example17" class="form-control form-control-lg " placeholder='ชื่อผู้ใช้' aria-label="Amount (to the nearest dollar)" />
                                                    <span class="input-group-text"><i class="fa-solid fa-user"></i></span>
                                                </div>

                                            </div>
                                            
                                            <div class="form-outline mb-4">
                                                <div class="input-group mb-3 in-shadow">
                                                    <input type="password" id="form2Example17" class="form-control form-control-lg " placeholder='รหัสผ่าน' aria-label="Amount (to the nearest dollar)" />
                                                    <span class="input-group-text"><i class="fa-solid fa-eye"></i></span>
                                                </div>
                                            </div> */}
                                        <div class="form-group position-relative mb-3 in-shadow">
                                            <input type="text" class="form-control is-valid p-2" name='par_username' placeholder='ป้อนชื่อผู้ใช้' id="par_username" onChange={handleChange} />
                                        </div>
                                        <div class="form-group position-relative mb-3 in-shadow">
                                            <input type="password" class="form-control is-valid p-2" name='par_password' placeholder='ป้อนรหัสผ่าน' id="par_password" onChange={handleChange} />
                                        </div>

                                        <div class="my-4">
                                            <button class="btn btn-theme btn-lg btn-block w-100" type="submit">เข้าสู่ระบบ</button>
                                        </div>
                                    </form>

                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ParentLog
