import React from 'react'
import { Link, useNavigate, useParams } from 'react-router-dom'
import Navparent from '../component/Navparent'
import Footer from '../component/Footer'
import { useEffect, useState } from 'react'
import { checkTokenAndRedirect } from '../component/authParent'
import axios from 'axios'

function ParentBehaviordetail() {

    const navigate = useNavigate();
    const localstorage = localStorage.getItem('TokenParent');
    const useToken = JSON.parse(localstorage);
    const { student_id, behaviour_id } = useParams();
    const [Data, setData] = useState([]);
    const [Data2, setData2] = useState([]);
    const [Data3, setData3] = useState([]);
    const [Data4, setData4] = useState([]);

    useEffect(() => {
        checkTokenAndRedirect(navigate);
        getData();
        getData2();
        getData3();
    }, []);

    const getData = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `student.php/${useToken.parent_id}/${student_id}/?xCase=16`);
            setData(response.data);
            console.log("data", response.data)
        } catch (error) {
            console.error(error);
        }
    };

    const getData2 = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `student.php/${useToken.parent_id}/${student_id}/${behaviour_id}/?xCase=19`);
            setData2(response.data);
            console.log("data2", response.data)
        } catch (error) {
            console.error(error);
        }
    };

    const getData3 = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `student.php/${useToken.parent_id}/${student_id}/${behaviour_id}/?xCase=20`);
            setData3(response.data);
            console.log("data3", response.data)
        } catch (error) {
            console.error(error);
        }
    };

    const handleChange = (event) => {
        const name = event.target.name;
        const value = event.target.value;
        setData4(values => ({ ...values, [name]: value }));
    }

    const handleSubmit = (event) => {
        event.preventDefault();
        var formData = new FormData();
        formData.append('comment_detail', Data4.comment_detail);
        axios.post(process.env.REACT_APP_API + `behavior.php/${useToken.parent_id}/${student_id}/${behaviour_id}/?xCase=4`, formData, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        }).then(function (response) {
            console.log(response.data);
            getData3();
            document.getElementById('comment_detail').value = '';
        });
    }

    return (
        <div>
            <Navparent />
            <section className='container'>
                <div className="card shadowcard">
                    <div className="card-body ">
                        <ol class="breadcrumb my-auto fw-bold">
                            <li class="breadcrumb-item"><Link className='text-dark' to="/ParentHome">หน้าหลัก</Link></li>
                            <li class="breadcrumb-item"><Link className='text-dark' to={`/ParentBehavior/${student_id}`}>พฤติกรรม {Data.student_prefix}{Data.student_firstname} {Data.student_lastname}</Link></li>
                            <li class="breadcrumb-item active color themepink">รายละเอียดพฤติกรรม</li>
                        </ol>
                    </div>
                </div>
            </section>
            <section className='container mb-5'>
                <div className="card shadowcard px-md-3 px-lg-5">
                    <div className="card-body">
                        <div className='fs-4 fw-bold text-center'>พฤติกรรม</div>
                        <div className="d-flex justify-content-center my-3">
                            <img src={process.env.REACT_APP_API + `students/` + Data.student_img} className='rounded-circle object-fit-cover' width={"150px"} height={"150px"} alt="" />
                        </div>
                        <div className='fs-5 fw-bold text-center'>
                            <p>{Data.student_prefix}{Data.student_firstname}{Data.student_lastname}</p>
                        </div>
                        <div className='fs-5 fw-bold text-center'>
                            <p>ครั้งที่ {Data2.b_odinal} วันที่ {Data2.Bthai_date}</p>
                        </div>
                        <hr />
                        <div className='fs-5 fw-bold'>รายละเอียดพฤติกรรม</div>
                        <div className='fs-6 descript my-3'>{Data2.behaviour_details}</div>
                        <hr />
                        <div className='fs-5 fw-bold'>ข้อความจากผู้ปกครอง</div>
                        {Data3.map((Datas) => (
                            <div className="card color bgegg my-3">
                                <div className="card-body">
                                    <div className='fs-6 fw-bold'>วันที่ {Datas.Bthai_date}</div>
                                    <p>
                                        {Datas.comment_detail}
                                    </p>
                                </div>
                            </div>
                        ))}
                        <div className="my-3">
                            <form className='mt-4' onSubmit={handleSubmit}>
                                <div className="row ">
                                    <div className="col-8 col-md-10 col-lg-11">
                                        <input className='form-control shadowcard' type="text" name="comment_detail" id='comment_detail' placeholder='ข้อความ' onChange={handleChange} />
                                    </div>
                                    <div className="col-4 col-md-2 col-lg-1">
                                        <button type='submit' className='btn btn-primary w-100 shadowcard' >ส่ง</button>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </section>
            <Footer />
        </div>
    )
}

export default ParentBehaviordetail
