import React from 'react'
import { Link, useNavigate, useParams } from 'react-router-dom'
import Navparent from '../component/Navparent'
import Footer from '../component/Footer'
import { useEffect, useState } from 'react'
import { checkTokenAndRedirect } from '../component/authParent'
import axios from 'axios'
import DataTable from 'react-data-table-component'

function ParentHomeworkdetail() {

    const navigate = useNavigate();
    const localstorage = localStorage.getItem('TokenParent');
    const useToken = JSON.parse(localstorage);
    const { student_id, teaching_id } = useParams();
    const [Data, setData] = useState([]);
    const [Data2, setData2] = useState([]);
    const [Data3, setData3] = useState([]);
    const [Data4, setData4] = useState([]);
    const [searchTerm, setSearchTerm] = useState('');
    const [searchResults, setSearchResults] = useState([]);

    useEffect(() => {
        checkTokenAndRedirect(navigate);
        getData();
        getData2();
        getData3();
        getData4();
    }, []);

    const getData = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `student.php/${useToken.parent_id}/${student_id}/?xCase=16`);
            setData(response.data);
            console.log("data1", response.data)
        } catch (error) {
            console.error(error);
        }
    };

    const getData2 = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `homework.php/${student_id}/${teaching_id}/?xCase=7`);
            setData2(response.data);
            console.log("data2", response.data)
        } catch (error) {
            console.error(error);
        }
    };

    const getData3 = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `homework.php/${student_id}/${teaching_id}/?xCase=8`);
            setData3(response.data);
            console.log("data3", response.data)
        } catch (error) {
            console.error(error);
        }
    };

    const getData4 = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `student.php/${student_id}/${teaching_id}/?xCase=17`);
            setData4(response.data);
            console.log("data4", response.data)
        } catch (error) {
            console.error(error);
        }
    };

    const handleSearchChange = (event) => {
        const searchTerm = event.target.value;

        // ตรวจสอบว่า Data ถูกกำหนดค่าและไม่ว่าง
        if (Data2 && Data2.length > 0) {
            const filteredResults = Data2.filter((Datas) =>
                ((Datas.homework_name || '').toString().toLowerCase().includes(searchTerm.toLowerCase())) ||
                (Datas.dua_thaidate || '').toString().toLowerCase().includes(searchTerm.toLowerCase()) ||
                (Datas.statusHomework || '').toLowerCase().includes(searchTerm.toLowerCase())
            );

            setSearchTerm(searchTerm);
            setSearchResults(filteredResults);
        } else {
            // จัดการกรณีที่ Data เป็น undefined หรือว่าง
            setSearchTerm(searchTerm);
            setSearchResults(Data2); // ใช้ Data เมื่อไม่มีคำค้นหา
        }
    };

    const customStyles = {
        rows: {
            style: {
                minHeight: '72px',
            },
        },
        headCells: {
            style: {
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                fontSize: '18px',
                fontWeight: 'bold'
            },
        },
        cells: {
            style: {
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                fontSize: '18px',
            },
        },

    };

    //ตารางการเข้าออก
    const columns = [
        { name: 'การบ้าน', selector: (row) => `${row.homework_name}`, sortable: true, headerCell: () => <div className="mx-auto "></div> },
        { name: 'กำหนดส่ง', selector: (row) => `${row.dua_thaidate}` },
        { name: 'สถานะการส่งการบ้าน', selector: (row) => `${row.statusHomework}` },
        {
            name: '',
            cell: (row) => (
                <div className='d-flex align-items-center'>
                    <button className='btn btn-outline-theme ' data-bs-toggle="modal" data-bs-target={`#ModalHW` + row.st_homework}  >ดูรายละเอียด
                    </button>
                </div>
            ),
        },
    ];

    return (
        <div>
            <Navparent />
            <section className='container'>
                <div className="card shadowcard">
                    <div className="card-body ">
                        <ol class="breadcrumb my-auto fw-bold">
                            <li class="breadcrumb-item"><Link className='text-dark' to="/ParentHome">หน้าหลัก</Link></li>
                            <li class="breadcrumb-item"><Link className='text-dark' to={`/ParentHomework/${student_id}`}>การบ้าน {Data.student_prefix}{Data.student_firstname} {Data.student_lastname}</Link></li>
                            <li class="breadcrumb-item active color themepink">วิชา{Data3.subject_name}</li>
                        </ol>
                    </div>
                </div>
            </section>
            <section className='container'>
                <div className="card shadowcard my-3">
                    <div className='fs-4 mt-3 fw-bold text-center'>ข้อมูลการบ้าน วิชา{Data3.subject_name}</div>
                    <div className="row d-flex justify-content-end">
                        <div className="col-5 col-lg-4 me-4">
                            <input type="text" className="form-control" value={searchTerm} onChange={handleSearchChange} placeholder="ค้นหา" />
                        </div>
                    </div>
                    <div className="card-body">
                        <DataTable
                            title=""
                            columns={columns}
                            data={searchTerm ? searchResults : Data2}
                            noDataComponent={<div>ไม่มีข้อมูลที่จะแสดง</div>}
                            customStyles={customStyles}
                            pagination
                            responsive
                            highlightOnHover
                            pointerOnHover
                            noHeader
                            className='rounded-3'
                        />
                    </div>
                    {Data2.map((Datas) => (
                        <div className="modal fade" id={`ModalHW${Datas.st_homework}`} tabIndex="-1" aria-labelledby={`ModalHWLabel${Datas.st_homework}`} aria-hidden="true">
                            <div className="modal-dialog modal-dialog-centered">
                                <div className="modal-content">
                                    <div className="modal-body">
                                        <div className="mb-3 text-end">
                                            <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                                        </div>
                                        <p className="fs-6 text-dark">การบ้าน : {Datas.homework_name}</p>
                                        <p className="fs-6 text-dark">รายละเอียด : {Datas.homework_details}</p>
                                        <p className="fs-6 text-dark">กำหนดส่ง : {Datas.dua_thaidate}</p>
                                        <p className="fs-6 text-dark">สถานะการส่ง : <span className={`${Datas.statusHomework === 'ยังไม่ได้ส่ง' ? 'text-warning' : 'text-success'}`}>{Datas.statusHomework}</span></p>
                                        <div className="text-center">
                                            <img src={process.env.REACT_APP_API + `homeworks/` + Datas.homework_img} alt="" className='w-75 my-4' />
                                        </div>
                                    </div>

                                </div>
                            </div>
                        </div>
                    ))}

                </div>
            </section>
            <section className='container'>
                <div className="card shadowcard my-3">
                    <div className='fs-4 mt-3 fw-bold text-center'>ข้อมูลคุณครูผู้สอน</div>
                    <div className="card-body m-3">
                        <div className="row mx-auto">
                            <div className="col-lg-6 d-flex justify-content-center align-items-center">
                                <img src={process.env.REACT_APP_API + `teachers/` + Data4.teacher_img} className='rounded-circle object-fit-cover my-auto' alt="รูปเด็กนักเรียน" width={"250px"} height={"250px"} />
                            </div>
                            <div className="col-lg-6 my-auto fs-5">
                                <p><strong>ชื่อครูผู้สอน : </strong>{Data4.teacher_prefix}{Data4.teacher_firstname} {Data4.teacher_lastname}</p>
                                <p><strong>เบอร์ที่ติดต่อ : </strong>{Data4.teacher_phone}</p>
                                <p><strong>ไลน์ไอดี : </strong>{Data4.teacher_line}</p>
                                <p><strong>อีเมล : </strong>{Data4.teacher_email}</p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <Footer />
        </div>
    )
}

export default ParentHomeworkdetail
