import React from 'react'
import { Link, useNavigate, useParams } from 'react-router-dom'
import Navparent from '../component/Navparent'
import Footer from '../component/Footer'
import { useEffect, useState } from 'react'
import { checkTokenAndRedirect } from '../component/authParent'
import axios from 'axios'

function ParentBehavior() {

    const navigate = useNavigate();
    const localstorage = localStorage.getItem('TokenParent');
    const useToken = JSON.parse(localstorage);
    const { student_id } = useParams();
    const [Data, setData] = useState([]);
    const [Data2, setData2] = useState([]);

    useEffect(() => {
        checkTokenAndRedirect(navigate);
        getData();
        getData2();
    }, []);

    const getData = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `student.php/${useToken.parent_id}/${student_id}/?xCase=16`);
            setData(response.data);
            console.log("data", response.data)
        } catch (error) {
            console.error(error);
        }
    };

    const getData2 = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `student.php/${useToken.parent_id}/${student_id}/?xCase=18`);
            setData2(response.data);
            console.log("data2", response.data)
        } catch (error) {
            console.error(error);
        }
    };

    return (
        <div>
            <Navparent />
            <section className='container'>
                <div className="card shadowcard">
                    <div className="card-body ">
                        <ol class="breadcrumb my-auto fw-bold">
                            <li class="breadcrumb-item"><Link className='text-dark' to="/ParentHome">หน้าหลัก</Link></li>
                            <li class="breadcrumb-item active color themepink">พฤติกรรม {Data.student_prefix}{Data.student_firstname} {Data.student_lastname}</li>
                        </ol>
                    </div>
                </div>
            </section>
            <section className='container'>
                <div className="row my-4 mx-auto">
                    {Data2.map((Datas) => (
                        <div className="col-12 col-md-6 col-lg-4 mb-4">
                            <div className="card shadowcard h-100">
                                <div className="card-body w-100 h-100">
                                    <div className='color themepink fs-4 fw-bold'>
                                        ครั้งที่ {Datas.b_odinal}
                                    </div>
                                    <div className="fs-5">
                                        <p>{Datas.Bthai_date}</p>
                                    </div>
                                    <hr />
                                    <div className="truncate-indent my-2 fs-6">
                                        <p>
                                            {Datas.behaviour_details}
                                        </p>
                                    </div>
                                </div>
                                <div className="d-flex align-content-end justify-content-center my-3">
                                    <Link className='btn btn-outline-theme w-75' to={`/ParentBehavior/${student_id}/${Datas.behaviour_id}/detail`}>ดูรายละเอียด</Link>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </section>
            <Footer />
        </div>
    )
}

export default ParentBehavior
