import React from 'react'
import { Link, useNavigate, useParams } from 'react-router-dom'
import Navparent from '../component/Navparent'
import Footer from '../component/Footer'
import { useEffect, useState } from 'react'
import { checkTokenAndRedirect } from '../component/authParent'
import axios from 'axios'

function ParentHomework() {

    const navigate = useNavigate();
    const localstorage = localStorage.getItem('TokenParent');
    const useToken = JSON.parse(localstorage);
    const { student_id } = useParams();
    const [Data, setData] = useState([]);
    const [Data2, setData2] = useState([]);
    const [Data3, setData3] = useState([]);

    useEffect(() => {
        checkTokenAndRedirect(navigate);
        getData2();
        getData3();
    }, []);

    const getData2 = async () => {
        try {
            // ดึงข้อมูลนักเรียนจาก API
            const response1 = await axios.get(process.env.REACT_APP_API + `student.php/${useToken.parent_id}/${student_id}/?xCase=15`);
            const data1 = response1.data;
            setData(data1); // กำหนดค่าให้ Data1

            console.log("data1", data1);

            const data2Array = []; // สร้างอาร์เรย์เปล่าเพื่อเก็บข้อมูลการบ้าน
            for (const teaching of data1) {
                // ดึงข้อมูลการบ้านของแต่ละวิชา
                const response2 = await axios.get(process.env.REACT_APP_API + `homework.php/${student_id}/${teaching.teaching_id}/?xCase=6`);
                const responseData2 = response2.data;

                data2Array.push(responseData2); // เพิ่มข้อมูลการบ้านของแต่ละวิชาลงในอาร์เรย์ data2Array
            }

            setData2(data2Array); // กำหนดค่าให้ Data2 เป็นอาร์เรย์ที่เก็บข้อมูลการบ้านของทุกนักเรียน
            console.log("data2", data2Array);
        } catch (error) {
            console.error(error);
        }
    };

    const getData3 = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `student.php/${useToken.parent_id}/${student_id}/?xCase=16`);
            setData3(response.data);
            console.log("data1", response.data)
        } catch (error) {
            console.error(error);
        }
    };


    return (
        <div>
            <Navparent />
            <section className='container'>
                <div className="card shadowcard">
                    <div className="card-body ">
                        <ol class="breadcrumb my-auto fw-bold">
                            <li class="breadcrumb-item"><Link className='text-dark' to="/ParentHome">หน้าหลัก</Link></li>
                            <li class="breadcrumb-item active color themepink">การบ้าน {Data3.student_prefix}{Data3.student_firstname} {Data3.student_lastname}</li>
                        </ol>
                    </div>
                </div>
            </section>
            <section className='container'>
                <div className="row">
                    {Data.map((Datas) => (
                        <div className="col-12 col-md-6 col-lg-4 my-2" key={Datas.teaching_id}>
                            <div className="card shadowcard h-100">
                                <div className="card-body mx-2">
                                    <div className='fs-5 fw-bold'>
                                        <div className="row">
                                            <div className='col-12 fs-4 text-center'>
                                                {Datas.subject_name}
                                            </div>
                                            <div className='col-10 text-warning my-2'>
                                                ยังไม่ได้ส่งการบ้าน
                                            </div>
                                            <div className='col-2 text-end my-2'>
                                                {Data2.filter((homeworks) => homeworks.some((homework) => homework.teaching_id === Datas.teaching_id))
                                                    .map((homeworks) => homeworks[0].lenStatus0)[0] || 0}

                                            </div>
                                            <div className='col-10 text-success my-2'>
                                                ส่งการบ้านแล้ว
                                            </div>
                                            <div className='col-2 text-end my-2'>
                                                {Data2.filter((homeworks) => homeworks.some((homework) => homework.teaching_id === Datas.teaching_id))
                                                    .map((homeworks) => homeworks[0].lenStatus1)[0] || 0}

                                            </div>
                                            <div className='col-10 text-secondary my-2'>
                                                การบ้านทั้งหมด
                                            </div>
                                            <div className='col-2 text-end my-2'>
                                                {Data2.filter((homeworks) => homeworks.some((homework) => homework.teaching_id === Datas.teaching_id))
                                                    .map((homeworks) => homeworks[0].lenHomework)[0] || 0}


                                            </div>
                                        </div>
                                    </div>
                                    <div className='text-secondary my-2'>
                                        ครูผู้สอน : ครู{Datas.teacher_firstname} {Datas.teacher_lastname}
                                    </div>
                                    <hr />
                                    <div className='fs-5 text-center'>
                                        <Link to={`/ParentHomework/${student_id}/${Datas.teaching_id}/detail`} className='btn btn-outline-theme fs-6 w-50'>ดูการบ้านทั้งหมด</Link>
                                    </div>
                                </div>
                            </div>
                        </div>
                    ))}

                </div>
            </section>
            <Footer />
        </div>
    )
}

export default ParentHomework
