import React from 'react'
import './nav.css'
import { Link } from 'react-router-dom'

function Nav() {
    return (
        <div className='navbar navbar-expand-lg navbar-light navbar-shadow fixed-top grads'>
            <div className='container-fluid'>
                <Link to="/" className="navbar-brand text-white"><img src={process.env.REACT_APP_PATH + `Image/logo.png`} width="50" height="50" alt="" />โรงเรียนบ้านน้ำจำ</Link>
                <Link to="/Login" className='text-decoration-none text-white fs-5'>เข้าสู่ระบบ<i class="fa-regular fa-lock-keyhole ms-2"></i></Link>
            </div>
        </div>
    )
}

export default Nav
