import React from 'react'
import { Link } from 'react-router-dom'
import NavLogin from "./component/NavLogin"

function Login() {
    return (
        <div>
            <NavLogin />
            <div className="container my-5">
                <section>
                    <div className="row">
                        <div className="col-lg-6 col-sm-6 d-flex flex-column align-items-center justify-content-center">
                            <Link to="/ParentLog" className=' text-decoration-none'>
                                <div className='bg-light p-2 m-3 d-flex align-items-center justify-content-center circleLog parent-b border border-white border-5 d-flex align-items-center justify-content-center'>
                                    <div className="row">
                                        <div className="col-12">
                                            <i class="fa-sharp fa-solid fa-graduation-cap icon-log"></i>
                                        </div>
                                    </div>
                                </div>
                                <h3 className='bold-pur text-center'>ผู้ปกครอง</h3>
                            </Link>
                        </div>
                        <div className="col-lg-6 col-sm-6 d-flex flex-column align-items-center justify-content-center">
                            <Link to="/TeacherLog" className=' text-decoration-none'>
                                <div className='bg-light p-2 m-3 d-flex align-items-center justify-content-center circleLog teacher-p border border-white border-5 d-flex align-items-center justify-content-center'>
                                    <div className="row">
                                        <div className="col-12">
                                            <i class="fa-sharp fa-solid fa-book-copy icon-log"></i>
                                        </div>
                                    </div>
                                </div>
                                <h3 className='bold-pur text-center'>คุณครู</h3>
                            </Link>
                        </div>
                    </div>
                </section>
            </div>
        </div>
    )
}

export default Login
