import React from 'react'
import './nav.css'
import { Link } from 'react-router-dom'

function Nav() {
    return (
        <div className='navbar navbar-light nav-color' >
            <div className='container-fluid'>
                <Link to="/" className="navbar-brand color-theme"><img src={process.env.REACT_APP_PATH + `Image/logo.png`} width="50" height="50" alt="โลโก้สำหรับเมนู" />โรงเรียนบ้านน้ำจำ</Link>
            </div>
        </div>
    )
}

export default Nav
