import React from 'react'
import { Link, useNavigate, useParams } from 'react-router-dom'
import Navteacher from '../component/Navteacher'
import Footer from '../component/Footer'
import { useEffect, useState } from 'react'
import { checkTokenAndRedirect } from '../component/authTeacher'
import axios from 'axios'
import DataTable from 'react-data-table-component'
import Swal from 'sweetalert2'

function TeacherHWCHWDeatail() {

    const navigate = useNavigate();
    const { subject_id, subject_name, classroom_id, teaching_id, homework_id } = useParams();
    const [Data, setData] = useState([]);
    const [Data2, setData2] = useState([]);
    const [Data3, setData3] = useState([]);
    const [searchTerm, setSearchTerm] = useState('');
    const [searchResults, setSearchResults] = useState([]);

    useEffect(() => {
        checkTokenAndRedirect(navigate);
        getData();
        getData2();
        getData3();
    }, []);

    const updateData = (student_id) => {
        axios
            .post(process.env.REACT_APP_API + `homework.php/${classroom_id}/${teaching_id}/${homework_id}/${student_id}/?xCase=3`)
            .then(function (response) {
                console.log(response.data);
                getData3();
            })
            .catch(function (error) {
                console.error(error);
            });
    };

    function showAlertUpdate(student_id) {
        Swal.fire({
            title: 'อัพเดทข้อมูล?',
            text: "ยืนยันที่จะอัพเดทข้อมูลสถานะการส่งการบ้านของนักเรียน",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#009B19',
            cancelButtonColor: '#6c757d',
            confirmButtonText: 'ยืนยัน',
            cancelButtonText: 'ยกเลิก'
        }).then((result) => {
            if (result.isConfirmed) {
                console.log(student_id)
                updateData(student_id)
                Swal.fire({
                    position: 'top-center',
                    icon: 'success',
                    title: 'ลบข้อมูลสำเร็จ',
                    text: "คุณได้ทำการยืนยันลบข้อมูลเรียบร้อยแล้ว!",
                    showConfirmButton: false,
                    timer: 1500
                }
                )
            }
        })
    }

    const getData = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `classroom.php/${classroom_id}/?xCase=4`);
            setData(response.data);
            console.log(response.data)
        } catch (error) {
            console.error(error);
        }
    };

    const getData2 = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `homework.php/${classroom_id}/${teaching_id}/${homework_id}/?xCase=1`);
            setData2(response.data);
            console.log(response.data)
        } catch (error) {
            console.error(error);
        }
    };

    const getData3 = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `homework.php/${classroom_id}/${teaching_id}/${homework_id}/?xCase=2`);
            setData3(response.data);
            console.log(response.data)
        } catch (error) {
            console.error(error);
        }
    };

    const handleSearchChange = (event) => {
        const searchTerm = event.target.value;

        // ตรวจสอบว่า Data ถูกกำหนดค่าและไม่ว่าง
        if (Data3 && Data3.length > 0) {
            const filteredResults = Data3.filter((Datas) =>
                ((Datas.student_id || '').toString().toLowerCase().includes(searchTerm.toLowerCase())) ||
                ((Datas.student_prefix || '').toLowerCase().includes(searchTerm.toLowerCase())) ||
                ((Datas.student_firstname || '').toLowerCase().includes(searchTerm.toLowerCase())) ||
                (Datas.student_lastname || '').toLowerCase().includes(searchTerm.toLowerCase())
            );

            setSearchTerm(searchTerm);
            setSearchResults(filteredResults);
        } else {
            // จัดการกรณีที่ Data เป็น undefined หรือว่าง
            setSearchTerm(searchTerm);
            setSearchResults(Data3); // ใช้ Data เมื่อไม่มีคำค้นหา
        }
    };

    const customStyles = {
        rows: {
            style: {
                minHeight: '72px',
            },
        },
        headCells: {
            style: {
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                fontSize: '18px',
                fontWeight: 'bold'
            },
        },
        cells: {
            style: {
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                fontSize: '18px',
            },
        },

    };

    const columns = [
        {
            name: 'รหัสนักเรียน', selector: 'student_id', sortable: true, headerCell: () => <div className="mx-auto "></div>
        },
        { name: 'ชื่อ - นามสกุล', selector: (row) => `${row.student_prefix}${row.student_firstname} ${row.student_lastname}`, sortable: true },
        {
            name: 'การส่งการบ้าน',
            cell: (row) => (
                <div className='d-flex align-items-center'>
                    {row.homeworked_status === 0 ?
                        <button className={`text-light btn btn-warning`} onClick={() => showAlertUpdate(row.student_id)}>
                            ยังไม่ได้ส่งการบ้าน
                        </button>
                        :
                        <button className={`text-light btn btn-success`}>
                            ส่งการบ้านแล้ว
                        </button>}
                </div>
            ),
        },
    ];

    return (
        <div>
            <Navteacher />
            <section className='h-100 d-flex flex-column justify-content-center'>
                <section className='container mt-5'>
                    <div className="card shadowcard mt-3">
                        <div className="card-body ">
                            <ol class="breadcrumb my-auto fw-bold">
                                <li class="breadcrumb-item"><Link className='text-dark' to="/TeacherHomework">การบ้าน</Link></li>
                                <li class="breadcrumb-item"><Link className='text-dark' to={`/TeacherHomework/${subject_id}/${subject_name}`}>วิชา{subject_name}</Link></li>
                                <li class="breadcrumb-item"><Link className='text-dark' to={`/TeacherHomework/${subject_id}/${subject_name}/${classroom_id}/${teaching_id}`}>ชั้นป.{Data.class_name && Data.classroom_name ? (Data.class_name.slice(16, 17)) + "/" + (Data.classroom_name.slice(5, 6)) : 'ไม่มีข้อมูล'}</Link></li>
                                <li class="breadcrumb-item active color themepink">ดูรายละเอียดการบ้าน</li>
                            </ol>
                        </div>
                    </div>
                </section>
                <section className='container color theme'>
                    <div className="card shadowcard mb-4">
                        <div className='card-body fs-5 mx-lg-3'>
                            <p>กำหนดส่ง   : {Data2.dua_thaidate}</p>
                            {Data2.homework_img ? (
                                <div className="text-center">
                                    <img
                                        src={process.env.REACT_APP_API + `homeworks/` + Data2.homework_img}
                                        alt="" className='rounded w-25 my-3'
                                    />
                                </div>
                            ) : null}
                            <p>การบ้าน : {Data2.homework_name}</p>
                            <p>รายละเอียด    : {Data2.homework_details} </p>
                        </div>
                    </div>
                    <div className="card shadowcard">
                        <div className="card-body">
                            <div className='fw-bold fs-5'>รายชื่อนักเรียน</div>
                            <div className="row my-2 d-flex justify-content-end">
                                <div className="col-8 col-lg-4"><input type="text" className='form-control form-control-lg fs-6' value={searchTerm} onChange={handleSearchChange} placeholder='ค้นหา' /></div>
                            </div>
                            <div className=''>
                                <DataTable
                                    title=""
                                    columns={columns}
                                    data={searchTerm ? searchResults : Data3}
                                    customStyles={customStyles}
                                    pagination
                                    responsive
                                    highlightOnHover
                                    pointerOnHover
                                    noHeader
                                    className='rounded-3'
                                />
                            </div>
                        </div>
                    </div>
                </section>
            </section>

            <Footer />
        </div>
    )
}

export default TeacherHWCHWDeatail
