import React from 'react'
import { Link, useNavigate, useParams } from 'react-router-dom'
import Navteacher from '../component/Navteacher'
import Footer from '../component/Footer'
import { useEffect, useState } from 'react'
import { checkTokenAndRedirect } from '../component/authTeacher'
import axios from 'axios'
import DataTable from 'react-data-table-component'
import Swal from 'sweetalert2'

function TeacherHWCroom() {
    const [Switching, setSwitching] = useState(1);
    const [searchTerm, setSearchTerm] = useState('');
    const [searchResults, setSearchResults] = useState([]);
    const [searchTerm2, setSearchTerm2] = useState('');
    const [searchResults2, setSearchResults2] = useState([]);
    const navigate = useNavigate();
    const { subject_id, subject_name, classroom_id, teaching_id } = useParams();
    const [Data, setData] = useState([]);
    const [Data2, setData2] = useState([]);
    const [Data3, setData3] = useState([]);
    const [Data4, setData4] = useState([]);
    const [Data5, setData5] = useState([]);
    const [File_pic, setFile_pic] = useState([]);
    const [homeworkImg, setHomeworkImg] = useState("")

    useEffect(() => {
        checkTokenAndRedirect(navigate);
        getData();
        getData2();
        getData3();
        getData5();
    }, []);

    const switchItem = async (value) => {
        setSwitching(value)
    }

    const imgChange = (event) => {
        // setFile_pic(URL.createObjectURL(event.target.files[0]));
        setHomeworkImg(event.target.files[0])
        setFile_pic(URL.createObjectURL(event.target.files[0]));
    }

    const handleChange = (event) => {
        const name = event.target.name;
        const value = event.target.value;
        setData4(values => ({ ...values, [name]: value }));
    }

    //ค้นหาชื่อนักเรียน
    const handleSearchChange = (event) => {
        const searchTerm = event.target.value;

        // ตรวจสอบว่า Data ถูกกำหนดค่าและไม่ว่าง
        if (Data2 && Data2.length > 0) {
            const filteredResults = Data2.filter((Datas) =>
                ((Datas.student_id || '').toString().toLowerCase().includes(searchTerm.toLowerCase())) ||
                (Datas.student_prefix || '').toLowerCase().includes(searchTerm.toLowerCase()) ||
                (Datas.student_firstname || '').toLowerCase().includes(searchTerm.toLowerCase()) ||
                (Datas.student_lastname || '').toLowerCase().includes(searchTerm.toLowerCase()) ||
                (Datas.student_nickname || '').toLowerCase().includes(searchTerm.toLowerCase())
            );

            setSearchTerm(searchTerm);
            setSearchResults(filteredResults);
        } else {
            // จัดการกรณีที่ Data เป็น undefined หรือว่าง
            setSearchTerm(searchTerm);
            setSearchResults(Data2); // ใช้ Data เมื่อไม่มีคำค้นหา
        }
    };

    //ค้นหาการบ้าน
    const handleSearchChange2 = (event) => {
        const searchTerm2 = event.target.value;

        // ตรวจสอบว่า Data ถูกกำหนดค่าและไม่ว่าง
        if (Data3 && Data3.length > 0) {
            const filteredResults2 = Data3.filter((Datas) =>
                ((Datas.homework_date || '').toString().toLowerCase().includes(searchTerm2.toLowerCase())) ||
                ((Datas.dua_date || '').toString().toLowerCase().includes(searchTerm2.toLowerCase())) ||
                (Datas.homework_name || '').toLowerCase().includes(searchTerm2.toLowerCase())
            );

            setSearchTerm2(searchTerm2);
            setSearchResults2(filteredResults2);
        } else {
            // จัดการกรณีที่ Data เป็น undefined หรือว่าง
            setSearchTerm2(searchTerm2);
            setSearchResults2(Data3); // ใช้ Data เมื่อไม่มีคำค้นหา
        }
    };

    const customStyles = {
        rows: {
            style: {
                minHeight: '72px',
            },
        },
        headCells: {
            style: {
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                fontSize: '18px',
                fontWeight: 'bold'
            },
        },
        cells: {
            style: {
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                fontSize: '18px',
            },
        },

    };

    //ตารางการบ้าน
    const columns = [
        {
            name: 'การบ้าน', selector: 'homework_name', sortable: true, headerCell: () => <div className="mx-auto "></div>
        },
        { name: 'วันที่', selector: 'homework_thaidate', sortable: true },
        { name: 'กำหนดส่ง', selector: 'dua_thaidate', sortable: true },
        {
            name: '',
            cell: (row) => (
                <div className='d-flex align-items-center'>
                    <Link to={`/TeacherHomework/${subject_id}/${row.subject_name}/${row.classroom_id}/${row.teaching_id}/${row.homework_id}/HWdetail`}>
                        <button className='btn btn-theme'>
                            ดูรายละเอียดเพิ่มเติม
                        </button>
                    </Link>
                </div>
            ),
        },
    ];

    const handleSubmit = (event) => {
        event.preventDefault();
        var formData = new FormData();
        // var img_File = document.querySelector('#img');
        // formData.append("teacher_img", img_File.files[0]);
        console.log("homework_img", homeworkImg);
        formData.append("homework_img", homeworkImg);
        formData.append('homework_name', Data4.homework_name);
        formData.append('homework_details', Data4.homework_details);
        formData.append('dua_date', Data4.dua_date);
        axios.post(process.env.REACT_APP_API + `homework.php/${classroom_id}/${teaching_id}/?xCase=4`, formData, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        }).then(function (response) {
            console.log(response.data);
            showAlertInsert();
            navigate(switchItem(2));
            getData3();
        });
    }

    const showAlertInsert = () => {
        Swal.fire({
            position: 'top-center',
            icon: 'success',
            title: 'บันทึกเสร็จสิ้น',
            showConfirmButton: false,
            timer: 2000
        })
    };

    const getData = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `classroom.php/${classroom_id}/?xCase=4`);
            setData(response.data);
            console.log(response.data)
        } catch (error) {
            console.error(error);
        }
    };

    const getData2 = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `student.php/${classroom_id}/?xCase=8`);
            setData2(response.data);
            console.log(response.data)
        } catch (error) {
            console.error(error);
        }
    };

    const getData3 = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `homework.php/${classroom_id}/${teaching_id}/?xCase=0`);
            setData3(response.data);
            console.log(response.data);
        } catch (error) {
            console.error(error);
        }
    };

    const getData5 = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `homework.php/${classroom_id}/${teaching_id}/?xCase=5`);
            setData5(response.data);
            console.log(response.data);
        } catch (error) {
            console.error(error);
        }
    };

    return (
        <div>
            <Navteacher />
            <section className='h-100 d-flex flex-column justify-content-center'>
                <section className='container mt-5'>
                    <div className="card shadowcard mt-3">
                        <div className="card-body ">
                            <ol class="breadcrumb my-auto fw-bold">
                                <li class="breadcrumb-item"><Link className='text-dark' to="/TeacherHomework">การบ้าน</Link></li>
                                <li class="breadcrumb-item"><Link className='text-dark' to={`/TeacherHomework/${subject_id}/${subject_name}`}>วิชา{subject_name}</Link></li>
                                <li class="breadcrumb-item active color themepink">ชั้นป.{Data.class_name && Data.classroom_name ? (Data.class_name.slice(16, 17)) + "/" + (Data.classroom_name.slice(5, 6)) : 'ไม่มีข้อมูล'}</li>
                            </ol>
                        </div>
                    </div>
                </section>
                <section className='container color theme'>
                    <div className="fs-3 fw-bold text-center my-2">การบ้านวิชา{subject_name} ชั้นประถมศึกษาปีที่ {Data.class_name && Data.classroom_name ? (Data.class_name.slice(16, 17)) + "/" + (Data.classroom_name.slice(5, 6)) : 'ไม่มีข้อมูล'}</div>
                    <div className="row my-2 ">
                        <div className="col-12 col-lg-4 my-2"> <button className={`btn ${Switching == 1 ? "btn-theme w-100 text-white" : "btn-light w-100 color theme"} fs-5`} onClick={() => switchItem(1)}>รายชื่อนักเรียนในชั้นเรียน</button></div>
                        <div className="col-6 col-lg-4 my-2"> <button className={`btn ${Switching == 2 ? "btn-theme w-100 text-white" : "btn-light w-100 color theme"} fs-5`} onClick={() => switchItem(2)}>การบ้าน</button></div>
                        <div className="col-6 col-lg-4 my-2"> <button className={`btn ${Switching == 3 ? "btn-theme w-100 text-white" : "btn-light w-100 color theme"} fs-5`} onClick={() => switchItem(3)}>เพิ่มการบ้าน</button></div>
                    </div>
                    {/* list student */}
                    {Switching == 1 ?
                        <div>
                            <div className="row my-3 d-flex justify-content-end">
                                <div className="col-8 col-lg-4"><input type="text" className='form-control form-control-lg fs-6' value={searchTerm} onChange={handleSearchChange} placeholder='ค้นหา' /></div>
                            </div>
                            <div>
                                {searchResults.length > 0 ? <div className="row">
                                    {searchResults.map((Datas, key) => (
                                        <div className='col-6 col-md-4 col-lg-3 mx-auto my-3'>
                                            <div class="mx-auto">
                                                <div className="circle-student d-flex justify-content-center">
                                                    <div className='circle-student__inner'>
                                                        <img src={process.env.REACT_APP_API + `students/` + Datas.student_img} className='rounded-circle circle-student__wrapper object-fit-cover' alt="" />
                                                    </div>
                                                </div>
                                                <div className='fs-5 my-2'>
                                                    <div className='mx-auto fw-bold text-center'>{Datas.student_id}</div>
                                                    <div className='mx-auto fw-bold text-center'>{Datas.student_prefix}{Datas.student_firstname} {Datas.student_lastname}</div>
                                                    <div className='mx-auto fw-bold text-center'>{Datas.student_nickname}</div>
                                                </div>
                                            </div>
                                        </div>
                                    ))}
                                </div> : <div className="row">
                                    {Data2.map((Datas, key) => (
                                        <div className='col-6 col-md-4 col-lg-3 mx-auto my-3'>
                                            <div class="mx-auto">
                                                <div className="circle-student d-flex justify-content-center">
                                                    <div className='circle-student__inner'>
                                                        <img src={process.env.REACT_APP_API + `students/` + Datas.student_img} className='rounded-circle circle-student__wrapper object-fit-cover' alt="" />
                                                    </div>
                                                </div>
                                                <div className='fs-5 my-2'>
                                                    <div className='mx-auto fw-bold text-center'>{Datas.student_id}</div>
                                                    <div className='mx-auto fw-bold text-center'>{Datas.student_prefix}{Datas.student_firstname} {Datas.student_lastname}</div>
                                                    <div className='mx-auto fw-bold text-center'>{Datas.student_nickname}</div>
                                                </div>
                                            </div>
                                        </div>
                                    ))}
                                </div>}
                            </div>
                        </div>
                        //list homework
                        : Switching == 2 ?
                            <div>
                                <div className="row my-2 d-flex justify-content-end">
                                    <div className="col-8 col-lg-4"><input type="text" className='form-control form-control-lg fs-6' value={searchTerm2} onChange={handleSearchChange2} placeholder='ค้นหา' /></div>
                                </div>
                                <div>
                                    <div className="card my-3">
                                        <div className='fs-4 mt-3 fw-bold text-center'>การบ้าน</div>
                                        <div className='card-body fs-5'>
                                            <DataTable
                                                title=""
                                                columns={columns}
                                                data={searchTerm2 ? searchResults2 : Data3}
                                                noDataComponent={<div>ไม่มีข้อมูลที่จะแสดง</div>}
                                                customStyles={customStyles}
                                                pagination
                                                responsive
                                                highlightOnHover
                                                pointerOnHover
                                                noHeader
                                                className='rounded-3'
                                            />
                                        </div>
                                    </div>
                                </div>
                            </div>
                            //new homework
                            : Switching == 3 ?
                                <div>
                                    <div className='card shadowcard my-4'>
                                        <div className="card-body">
                                            <div className='fs-2 fw-bold text-center mb-3'>เพิ่มการบ้าน</div>
                                            <form onSubmit={handleSubmit}>
                                                <div className="row mx-4">
                                                    <div className="d-flex justify-content-center">
                                                        <img src={File_pic == '' ? process.env.REACT_APP_PATH + `no-image.png` : File_pic} className='rounded object-fit-cover mx-auto w-25' />
                                                    </div>
                                                    <div className="col-12 my-2">
                                                        <label htmlFor="" className="fs-5">รูปภาพ</label>
                                                        <input className="form-control" type="file" name="homework_img" id="img" onChange={imgChange} />
                                                    </div>
                                                    <div className="col-12 col-lg-6">
                                                        <label htmlFor="" className="col-form-label fw-bold">วันที่กำหนดส่ง :</label>
                                                        <input className="form-control" type="date" name="dua_date" id='dua_date' onChange={handleChange} />
                                                    </div>
                                                    <div className="col-12">
                                                        <label htmlFor="" className="col-form-label fw-bold">การบ้าน :</label>
                                                        <input className="form-control" type="text" name="homework_name" id='homework_name' placeholder="ป้อนหัวข้อการบ้าน" onChange={handleChange} />
                                                    </div>
                                                    <div className="col-12">
                                                        <label htmlFor="" className="col-form-label fw-bold">รายละเอียด :</label>
                                                        <textarea className="form-control" type="text" rows={5} name="homework_details" id='homework_details' placeholder="ป้อนรายละเอียดการบ้าน" onChange={handleChange} />
                                                    </div>
                                                </div>

                                                <div className='d-flex justify-content-center my-3'>
                                                    <button className='btn btn-success mx-2 px-5' type='submit' >บันทึก</button>
                                                </div>
                                            </form>
                                        </div>
                                    </div>
                                </div>
                                : ""}

                </section>
            </section>
            <Footer />
        </div>
    )
}

export default TeacherHWCroom
