import React from 'react'
import { Link, useNavigate } from 'react-router-dom'
import Navteacher from '../component/Navteacher'
import Footer from '../component/Footer'
import { useEffect, useState } from 'react'
import { checkTokenAndRedirect } from '../component/authTeacher'
import axios from 'axios'

function TeacherHomework() {

    const navigate = useNavigate();
    const localstorage = localStorage.getItem('TokenTeacher');
    const useToken = JSON.parse(localstorage);
    const [Data, setData] = useState([]);

    useEffect(() => {
        checkTokenAndRedirect(navigate);
        getData();
    }, []);

    const getData = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `teaching.php/${useToken.teacher_id}/?xCase=12`);
            setData(response.data);
        } catch (error) {
            console.error(error);
        }
    };

    return (
        <div>
            <Navteacher />
            <section className='h-100 d-flex flex-column justify-content-center'>
                <div className="text-white text-uppercase fw-bold text-center my-auto mt-3">
                    <div className='fs-1 fw-bold text-center mt-5 color themegrey'>
                        การบ้านวิชา
                    </div>
                    <div className='fs-5 fw-bold text-center color themegrey'>
                        เลือกวิชา
                    </div>
                    <div className="row my-4 justify-content-center">
                        {Data.map((Datas) => (
                            <div className='col-6 col-md-4 col-lg-4 my-1'>
                                <Link to={`/TeacherHomework/${Datas.subject_id}/${Datas.subject_name}`}>
                                    <div className="row d-flex flex-column">
                                        <div class="col-12 fs-1">
                                            <div class="circle-homework d-flex justify-content-center">
                                                <div class="circle-homework__inner">
                                                    <div class="circle-homework__wrapper">
                                                        <div class="circle-homework__content text-white fs-1">{Datas.subject_code}</div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="fs-4 col-12 mx-auto fw-bold color themegrey my-3 text-center">
                                            {Datas.subject_name}
                                        </div>
                                    </div>
                                </Link>
                            </div>
                        ))}
                    </div>
                </div>
            </section>
            <Footer />
        </div>
    )
}

export default TeacherHomework
