import React from 'react'
import { Link, useNavigate, useParams } from 'react-router-dom'
import Navteacher from '../component/Navteacher'
import Footer from '../component/Footer'
import { useEffect, useState } from 'react'
import { checkTokenAndRedirect } from '../component/authTeacher'
import axios from 'axios'
import Swal from 'sweetalert2'

function TeacherProfile() {

    const navigate = useNavigate();
    const localstorage = localStorage.getItem('TokenTeacher');
    const useToken = JSON.parse(localstorage);
    const [Data3, setData3] = useState([]); //getTypeTeacher
    const [Data4, setData4] = useState([]); //getAcademic
    const [Data5, setData5] = useState([]); //getTeacher
    const [teacherImg, setTeacherImg] = useState("")
    const [File_pic, setFile_pic] = useState([]);
    const [isChecked, setisChecked] = useState(true);

    useEffect(() => {
        checkTokenAndRedirect(navigate);
    }, []);

    const editChange = () => {
        setisChecked(!isChecked);
    }

    useEffect(() => {
        getTypeTeacher();
        getAcademic();
        getTeacher();
    }, []);

    const handleChange = (event) => {
        const name = event.target.name;
        const value = event.target.value;
        setData5(values => ({ ...values, [name]: value }));
    }

    const imgChange = (event) => {
        setTeacherImg(event.target.files[0])
        setFile_pic(URL.createObjectURL(event.target.files[0]));
    }

    const getTeacher = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `teacher.php/${useToken.teacher_id}/?xCase=4`);
            setData5(response.data);
        } catch (error) {
            console.error(error);
        }
    };


    const getTypeTeacher = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `type_teacher.php/?xCase=0`);
            setData3(response.data);
        } catch (error) {
            console.error(error);
        }
    };

    const getAcademic = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `academic.php/?xCase=0`);
            setData4(response.data);
        } catch (error) {
            console.error(error);
        }
    };

    const handleSubmit = (event) => {
        event.preventDefault();
        var formData = new FormData();
        formData.append("teacher_img", teacherImg);
        formData.append('teacher_id', Data5.teacher_id);
        formData.append('teacher_cardid', Data5.teacher_cardid);
        formData.append('teacher_prefix', Data5.teacher_prefix);
        formData.append('teacher_firstname', Data5.teacher_firstname);
        formData.append('teacher_lastname', Data5.teacher_lastname);
        formData.append('teacher_department', Data5.teacher_department);
        formData.append('typeTeacher_id', Data5.typeTeacher_id);
        formData.append('academic_id', Data5.academic_id);
        formData.append('teacher_address', Data5.teacher_address);
        formData.append('teacher_moo', Data5.teacher_moo);
        formData.append('teacher_subdis', Data5.teacher_subdis);
        formData.append('teacher_district', Data5.teacher_district);
        formData.append('teacher_province', Data5.teacher_province);
        formData.append('teacher_postcode', Data5.teacher_postcode);
        formData.append('teacher_phone', Data5.teacher_phone);
        formData.append('teacher_line', Data5.teacher_line);
        formData.append('teacher_email', Data5.teacher_email);
        formData.append('teachlog_username', Data5.teachlog_username);
        formData.append('teachlog_password', Data5.teachlog_password);

        axios.post(process.env.REACT_APP_API + `teacher.php/${useToken.teacher_id}/?xCase=3`, formData, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        }).then(function (response) {
            console.log(response.data);
            // navigate(`/TeacherProfile`);
            setisChecked(true);
            showAlertSubmit();
        });
    }

    const showAlertSubmit = () => {
        Swal.fire({
            position: 'top-center',
            icon: 'success',
            title: 'บันทึกเสร็จสิ้น',
            showConfirmButton: false,
            timer: 2000
        })
    };

    return (
        <div>
            <Navteacher />
            <section className='h-100 d-flex flex-column justify-content-center'>
                <div className="container my-5">
                    <div className="card shadowcard mt-3">
                        <div className='fs-4 my-3 fw-bold text-center'>{isChecked ? 'ข้อมูลส่วนตัว' : 'แก้ไขข้อมูลส่วนตัว'}</div>
                        <div className="card-body">
                            <div className="d-flex justify-content-center mb-3">
                                <img src={File_pic == '' ? process.env.REACT_APP_API + `teachers/${Data5.teacher_img}` : File_pic} className='rounded-circle object-fit-cover mx-auto' width={'200px'} height={'200px'} />
                            </div>
                            <div className="row mx-md-3 mx-lg-5">
                                {isChecked ? '' :
                                    <div className="col-12 my-2">
                                        <label htmlFor="" class="fs-5">รูปภาพ</label>
                                        <input class="form-control" type="file" name="teacher_img" id="img" onChange={imgChange} />
                                    </div>
                                }
                                <div className="col-12 my-2">
                                    <label htmlFor="" className='fs-5'>เลขบัตรประชาชน</label>
                                    <input type="text" name="teacher_cardid" id="teacher_cardid" class="form-control" maxLength="13" onChange={handleChange} value={Data5.teacher_cardid} disabled={isChecked} />
                                </div>
                                <div className="col-2 my-2">
                                    <label htmlFor="" className='fs-5'>คำนำหน้าชื่อ</label>
                                    <input type="text" name="teacher_prefix" id="teacher_prefix" class="form-control" onChange={handleChange} value={Data5.teacher_prefix} disabled={isChecked} />
                                </div>
                                <div className="col-5 my-2">
                                    <label htmlFor="" className='fs-5'>ชื่อ</label>
                                    <input type="text" name="teacher_firstname" id="teacher_firstname" class="form-control" onChange={handleChange} value={Data5.teacher_firstname} disabled={isChecked} />
                                </div>
                                <div className="col-5 my-2">
                                    <label htmlFor="" className='fs-5'>นามสกุล</label>
                                    <input type="text" name="teacher_lastname" id="teacher_lastname" class="form-control" onChange={handleChange} value={Data5.teacher_lastname} disabled={isChecked} />
                                </div>
                                <div className="col-4 my-2">
                                    <label htmlFor="" className='fs-5'>ตำแหน่ง</label>
                                    <input type="text" name="teacher_department" id="teacher_department" class="form-control" onChange={handleChange} value={Data5.teacher_department} disabled={isChecked} />
                                </div>
                                <div className="col-4 my-2">
                                    <label htmlFor="" className='fs-5'>ประเภทบุคลากร</label>
                                    <select class="form-select" aria-label="Default select example" name="typeTeacher_id" onChange={handleChange} value={Data5.typeTeacher_id} disabled>
                                        <option selected disabled>เลือกประเภทบุคลากร</option>
                                        {Data3.map((Datas, key) => (
                                            <option value={Datas.typeTeacher_id}>{Datas.typeTeacher_name}</option>
                                        ))}
                                    </select>
                                </div>
                                <div className="col-4 my-2">
                                    <label htmlFor="" className='fs-5'>วิทยฐานะ</label>
                                    <select class="form-select" aria-label="Default select example" name="academic_id" onChange={handleChange} value={Data5.academic_id} disabled>
                                        <option selected disabled>เลือกวิทยฐานะ</option>
                                        {Data4.map((Datas, key) => (
                                            <option value={Datas.academic_id}>{Datas.academic_name}</option>
                                        ))}
                                    </select>
                                </div>
                                <div className="col-2 my-2">
                                    <label htmlFor="" className='fs-5'>ที่อยู่</label>
                                    <input type="text" name="teacher_address" id="teacher_address" class="form-control" onChange={handleChange} value={Data5.teacher_address} disabled={isChecked} />
                                </div>
                                <div className="col-2 my-2">
                                    <label htmlFor="" className='fs-5'>หมู่</label>
                                    <input type="text" name="teacher_moo" id="teacher_moo" class="form-control" onChange={handleChange} value={Data5.teacher_moo} disabled={isChecked} />
                                </div>
                                <div className="col-4 my-2">
                                    <label htmlFor="" className='fs-5'>ตำบล</label>
                                    <input type="text" name="teacher_subdis" id="teacher_subdis" class="form-control" onChange={handleChange} value={Data5.teacher_subdis} disabled={isChecked} />
                                </div>
                                <div className="col-4 my-2">
                                    <label htmlFor="" className='fs-5'>อำเภอ</label>
                                    <input type="text" name="teacher_district" id="teacher_district" class="form-control" onChange={handleChange} value={Data5.teacher_district} disabled={isChecked} />
                                </div>
                                <div className="col-6 my-2">
                                    <label htmlFor="" className='fs-5'>จังหวัด</label>
                                    <input type="text" name="teacher_province" id="teacher_province" class="form-control" onChange={handleChange} value={Data5.teacher_province} disabled={isChecked} />
                                </div>
                                <div className="col-6 my-2">
                                    <label htmlFor="" className='fs-5'>รหัสไปรษณีย์</label>
                                    <input type="text" name="teacher_postcode" id="teacher_postcode" class="form-control" maxLength="5" onChange={handleChange} value={Data5.teacher_postcode} disabled={isChecked} />
                                </div>
                                <div className="col-6 my-2">
                                    <label htmlFor="" className='fs-5'>เบอร์ที่ติดต่อ</label>
                                    <input type="text" name="teacher_phone" id="teacher_phone" class="form-control" maxLength={10} onChange={handleChange} value={Data5.teacher_phone} disabled={isChecked} />
                                </div>
                                <div className="col-6 my-2">
                                    <label htmlFor="" className='fs-5'>อีเมล</label>
                                    <input type="text" name="teacher_email" id="teacher_email" class="form-control" onChange={handleChange} value={Data5.teacher_email} disabled={isChecked} />
                                </div>
                                <div className="col-6 my-2">
                                    <label htmlFor="" className='fs-5'>ไลน์ไอดี</label>
                                    <input type="text" name="teacher_line" id="teacher_line" class="form-control" onChange={handleChange} value={Data5.teacher_line} disabled={isChecked} />
                                </div>
                                <div className="col-6 my-2">
                                    <label htmlFor="" className='fs-5' hidden>ชื่อผู้ใช้</label>
                                    <input type="text" name="teachlog_username" id="teachlog_username" class="form-control" onChange={handleChange} value={Data5.teachlog_username} disabled={isChecked} hidden />
                                </div>
                                <div className="col-6 my-2">
                                    <label htmlFor="" className='fs-5' hidden>รหัสผ่าน</label>
                                    <input type="password" name="teachlog_password" id="teachlog_password" class="form-control" onChange={handleChange} value={Data5.teachlog_password} disabled={isChecked} hidden />
                                </div>
                            </div>
                            <div className="d-flex justify-content-center my-3">
                                {isChecked ?
                                    (<div><button type='button' className='btn btn-warning mx-3 mt-3 text-light' onClick={() => editChange()}>แก้ไขข้อมูล</button> </div>)
                                    : <div><button type='button' className='btn btn-success mx-3 mt-3' onClick={handleSubmit}>บันทึกข้อมูล</button></div>
                                }
                            </div>
                        </div>
                    </div>
                </div>
            </section>

            <Footer />
        </div>
    )
}

export default TeacherProfile
