import React from 'react'
import Navteacher from '../component/Navteacher'
import Footer from '../component/Footer'
import { Link, json, useNavigate, useParams } from 'react-router-dom'
import { useEffect, useState } from 'react'
import { checkTokenAndRedirect } from '../component/authTeacher'
import axios from 'axios'

function TeacherBehavior() {

    const navigate = useNavigate();
    const { student_id } = useParams();
    const [Data, setData] = useState([]);

    useEffect(() => {
        checkTokenAndRedirect(navigate);
        getData();
    }, []);

    const getData = async () => {
        try {
            const response = await axios.get(process.env.REACT_APP_API + `behavior.php/${student_id}/?xCase=0`);
            setData(response.data);
        } catch (error) {
            console.error(error);
        }
    };

    return (
        <div>
            <Navteacher />
            <section className='h-100 d-flex flex-column justify-content-center'>
                <section className='container mt-5'>
                    <div className="card shadowcard mt-3">
                        <div className="card-body ">
                            <ol class="breadcrumb my-auto fw-bold">
                                <li class="breadcrumb-item"><Link className='text-dark' to="/TeacherHome">ชั้นเรียนที่ดูแล</Link></li>
                                <li class="breadcrumb-item"><Link className='text-dark' to={`/TeacherDataStudent/${student_id}`}>รายละเอียด เด็กชายก้านกล้วย</Link></li>
                                <li class="breadcrumb-item active color themepink">พฤติกรรม</li>
                            </ol>
                        </div>
                    </div>
                </section>
                <section className='container'>
                    <div className='row'>
                        <div className='d-flex align-items-center my-3'>
                            <div className="col-6 col-lg-8">
                                <Link className='btn btn-theme' to={`/TeacherBehavior/${student_id}/new`}>เพิ่มรายงาน</Link>
                            </div>
                            <div className="col-6 col-lg-4">
                                <input type="text" name="" className='form-control' placeholder='ค้นหา' id="" />
                            </div>
                        </div>
                        <div className='row mb-3 mx-auto'>
                            {/* Start content */}
                            {Data.map((Datas) => (
                                <div className="col-12 col-md-6 col-lg-4 my-3">
                                    <div className="card shadowcard h-100">
                                        <div className="card-body w-100 h-100">
                                            <div className='color themepink fs-4 fw-bold'>
                                                ครั้งที่ {Datas.b_odinal}
                                            </div>
                                            <p>
                                                วันที่ {Datas.thai_date}
                                            </p>
                                            <hr />
                                            <div className='descript truncate-text'>
                                                {Datas.behaviour_details}
                                            </div>
                                        </div>
                                        <div className="d-flex align-content-end justify-content-center my-3">
                                            <Link className='btn btn-outline-theme w-75' to={`/TeacherBehaviorDetail/${student_id}/${Datas.behaviour_id}`}>ดูรายละเอียด</Link>
                                        </div>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                </section>
            </section>
            <Footer />
        </div>
    )
}

export default TeacherBehavior
